import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import { apiBaseUrl } from '../config';

const ProfileEdit = () => {
  const [formData, setFormData] = useState({
    name: '',
    gender: '',
    address: '',
    pincode: '',
    state: '',
    preferred_language_id: '',
    looking_for_languages: [],
    bio: '',
    interests: [],
    relationship_goal: ''
  });
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');
  const navigate = useNavigate();
  const { user, login } = useAuth();

  // Language options
  const languages = [
    { id: 1, name: 'English', flag: '' },
    { id: 2, name: 'Hindi', flag: '' },
    { id: 3, name: 'Punjabi', flag: '' },
    { id: 4, name: 'Bengali', flag: '' },
    { id: 5, name: 'Telugu', flag: '' },
    { id: 6, name: 'Marathi', flag: '' }
  ];

  // Interest options
  const interestOptions = [
    'technology', 'gaming', 'movies', 'music', 'sports', 'fitness',
    'food', 'travel', 'fashion', 'education', 'learning', 'culture',
    'art', 'photography', 'cooking', 'reading', 'dancing', 'singing'
  ];

  // Relationship goal options
  const relationshipGoals = [
    'friendship', 'casual', 'serious', 'marriage'
  ];

  // Load existing user data
  useEffect(() => {
    if (user) {
      setFormData({
        name: user.name || '',
        gender: user.gender || '',
        address: user.address || '',
        pincode: user.pincode || '',
        state: user.state || '',
        preferred_language_id: user.preferred_language_id || '',
        looking_for_languages: user.looking_for_languages || [],
        bio: user.bio || '',
        interests: user.interests || [],
        relationship_goal: user.relationship_goal || ''
      });
    }
  }, [user]);

  // Redirect if no user
  useEffect(() => {
    if (!user) {
      navigate('/login');
    }
  }, [user, navigate]);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleCheckboxChange = (name, value) => {
    setFormData(prev => ({
      ...prev,
      [name]: prev[name].includes(value)
        ? prev[name].filter(item => item !== value)
        : [...prev[name], value]
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    // Validate form
    if (!formData.name.trim()) {
      setMessage({ type: 'error', text: 'Name is required' });
      return;
    }
    if (!formData.gender) {
      setMessage({ type: 'error', text: 'Please select your gender' });
      return;
    }
    if (!formData.preferred_language_id) {
      setMessage({ type: 'error', text: 'Please select your preferred language' });
      return;
    }
    if (formData.looking_for_languages.length === 0) {
      setMessage({ type: 'error', text: 'Please select at least one language you want to connect with' });
      return;
    }
    if (formData.interests.length === 0) {
      setMessage({ type: 'error', text: 'Please select at least one interest' });
      return;
    }
    if (!formData.relationship_goal) {
      setMessage({ type: 'error', text: 'Please select your relationship goal' });
      return;
    }

    setLoading(true);
    setMessage('');

    // Debug logging
    const accessToken = localStorage.getItem('authToken');
    console.log(' ProfileEdit Debug - Access Token:', accessToken);
    console.log(' ProfileEdit Debug - Form Data:', formData);
    console.log(' ProfileEdit Debug - User:', user);

    try {
      // Update user profile in backend
      const response = await fetch(`${apiBaseUrl}/api/users/update-profile`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${accessToken}`
        },
        body: JSON.stringify({
          name: formData.name,
          gender: formData.gender,
          preferred_language_id: formData.preferred_language_id,
          looking_for_languages: formData.looking_for_languages,
          bio: formData.bio,
          interests: formData.interests,
          relationship_goal: formData.relationship_goal
        })
      });

      console.log(' ProfileEdit Debug - Response Status:', response.status);
      console.log(' ProfileEdit Debug - Response Headers:', response.headers);

      if (response.ok) {
        const updatedUser = { ...user, ...formData };
        login(updatedUser, accessToken);
        setMessage({ type: 'success', text: 'Profile updated successfully!' });
        setTimeout(() => navigate('/calls'), 1500);
      } else {
        const errorData = await response.json();
        console.error(' ProfileEdit Debug - Error Response:', errorData);
        setMessage({ type: 'error', text: errorData.error || 'Failed to update profile' });
      }
    } catch (error) {
      console.error(' ProfileEdit Debug - Fetch Error:', error);
      setMessage({ type: 'error', text: 'Failed to update profile. Please try again.' });
    } finally {
      setLoading(false);
    }
  };

  if (!user) {
    return null; // Will redirect in useEffect
  }

  return (
    <div className="login-container">
      <div className="login-content">
        {/* 1. TOP: Logo and Brand Name */}
        <div className="header-section">
          <div className="brand-container">
            <img 
              src="https://akm-dv.github.io/unlimitedhub/logo.jpg" 
              alt="Unlimited Hub Logo" 
              className="brand-logo"
            />
            <h1 className="brand-name">Unlimited Hub</h1>
          </div>
        </div>

        {/* 2. BELOW: Illustration */}
        <div className="illustration-section">
          <img 
            src="https://cdni.iconscout.com/illustration/premium/thumb/profile-completion-5155859-4309052.png" 
            alt="Profile Edit Illustration" 
            className="dating-illustration"
          />
        </div>

        {/* 3. BELOW: Content/Description */}
        <div>
          <h2 className="tagline-title">Edit Your Profile</h2>
          <p className="tagline-subtitle">Update your language preferences and interests</p>
        </div>

        {/* 4. BOTTOM: Profile Edit Form Container */}
        <div className="form-section">
          {message && (
            <div className={`message ${message.type}`}>
              {message.text}
            </div>
          )}

          <form onSubmit={handleSubmit} className="login-form">
            <div className="form-group">
              <label className="form-label">Full Name *</label>
              <input
                type="text"
                name="name"
                value={formData.name}
                onChange={handleInputChange}
                className="form-input"
                placeholder="Enter your full name"
                required
              />
              <p style={{ fontSize: '0.8rem', color: '#666', marginTop: '0.5rem' }}>
                This will be your display name
              </p>
            </div>

            <div className="form-group">
              <label className="form-label">Gender *</label>
              <select
                name="gender"
                value={formData.gender}
                onChange={handleInputChange}
                className="form-input"
                required
              >
                <option value="">Select your gender</option>
                <option value="male">Male</option>
                <option value="female">Female</option>
              </select>
              <p style={{ fontSize: '0.8rem', color: '#666', marginTop: '0.5rem' }}>
                This helps us match you better
              </p>
            </div>

            <div className="form-group">
              <label className="form-label">Address</label>
              <input
                type="text"
                name="address"
                value={formData.address}
                onChange={handleInputChange}
                className="form-input"
                placeholder="Enter your address (optional)"
              />
              <p style={{ fontSize: '0.8rem', color: '#666', marginTop: '0.5rem' }}>
                Optional: Your location for better matches
              </p>
            </div>

            <div className="form-group">
              <label className="form-label">Pincode</label>
              <input
                type="text"
                name="pincode"
                value={formData.pincode}
                onChange={handleInputChange}
                className="form-input"
                placeholder="Enter your pincode (optional)"
                maxLength="10"
              />
              <p style={{ fontSize: '0.8rem', color: '#666', marginTop: '0.5rem' }}>
                Optional: Your area pincode
              </p>
            </div>

            <div className="form-group">
              <label className="form-label">State</label>
              <input
                type="text"
                name="state"
                value={formData.state}
                onChange={handleInputChange}
                className="form-input"
                placeholder="Enter your state (optional)"
              />
              <p style={{ fontSize: '0.8rem', color: '#666', marginTop: '0.5rem' }}>
                Optional: Your state or region
              </p>
            </div>

            <div className="form-group">
              <label className="form-label">Your Preferred Language *</label>
              <select
                name="preferred_language_id"
                value={formData.preferred_language_id}
                onChange={handleInputChange}
                className="form-input"
                required
              >
                <option value="">Select your primary language</option>
                {languages.map((lang) => (
                  <option key={lang.id} value={lang.id}>
                    {lang.flag} {lang.name}
                  </option>
                ))}
              </select>
              <p style={{ fontSize: '0.8rem', color: '#666', marginTop: '0.5rem' }}>
                This is the language you're most comfortable with
              </p>
            </div>

            <div className="form-group">
              <label className="form-label">Languages You Want to Connect With *</label>
              <div className="checkbox-group">
                {languages.map((lang) => (
                  <label key={lang.id} className="checkbox-option">
                    <input
                      type="checkbox"
                      checked={formData.looking_for_languages.includes(lang.id)}
                      onChange={() => handleCheckboxChange('looking_for_languages', lang.id)}
                    />
                    <span className="checkbox-label">{lang.flag} {lang.name}</span>
                  </label>
                ))}
              </div>
              <p style={{ fontSize: '0.8rem', color: '#666', marginTop: '0.5rem' }}>
                Select languages you want to practice or connect with
              </p>
            </div>

            <div className="form-group">
              <label className="form-label">Bio</label>
              <textarea
                name="bio"
                value={formData.bio}
                onChange={handleInputChange}
                className="form-input"
                placeholder="Tell us about yourself..."
                rows="3"
                style={{ resize: 'vertical', minHeight: '80px' }}
              />
              <p style={{ fontSize: '0.8rem', color: '#666', marginTop: '0.5rem' }}>
                Optional: Share something about yourself
              </p>
            </div>

            <div className="form-group">
              <label className="form-label">Your Interests *</label>
              <div className="checkbox-group">
                {interestOptions.map((interest) => (
                  <label key={interest} className="checkbox-option">
                    <input
                      type="checkbox"
                      checked={formData.interests.includes(interest)}
                      onChange={() => handleCheckboxChange('interests', interest)}
                    />
                    <span className="checkbox-label">{interest}</span>
                  </label>
                ))}
              </div>
              <p style={{ fontSize: '0.8rem', color: '#666', marginTop: '0.5rem' }}>
                Select your interests to find like-minded people
              </p>
            </div>

            <div className="form-group">
              <label className="form-label">Relationship Goal *</label>
              <select
                name="relationship_goal"
                value={formData.relationship_goal}
                onChange={handleInputChange}
                className="form-input"
                required
              >
                <option value="">Select your relationship goal</option>
                {relationshipGoals.map((goal) => (
                  <option key={goal} value={goal}>
                    {goal.charAt(0).toUpperCase() + goal.slice(1)}
                  </option>
                ))}
              </select>
              <p style={{ fontSize: '0.8rem', color: '#666', marginTop: '0.5rem' }}>
                What are you looking for in a relationship?
              </p>
            </div>

            <button 
              type="submit" 
              className="primary-btn"
              disabled={loading}
            >
              {loading ? 'Updating Profile...' : 'Update Profile'}
            </button>
          </form>

          <div style={{ textAlign: 'center', marginTop: '1rem' }}>
            <button
              type="button"
              className="back-btn"
              onClick={() => navigate('/calls')}
            >
               Back to Calls
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProfileEdit;
