import React, { useState, useEffect } from 'react';
import ReactDOM from 'react-dom';
import { useAuth } from '../context/AuthContext';
import axios from 'axios';
import { apiBaseUrl } from '../config';

const Step1RegistrationModal = ({ isOpen, onClose, phone, onSuccess }) => {
  const { login } = useAuth();
  const [formData, setFormData] = useState({
    name: '',
    gender: ''
  });
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');
  const [suggestedNames, setSuggestedNames] = useState([]);

  // Generate suggested names based on gender
  const generateSuggestedNames = (gender) => {
    const nameSuggestions = {
      male: ['Aryan', 'Raj', 'Vikram', 'Arjun', 'Rohit', 'Siddharth', 'Karan', 'Rahul'],
      female: ['Priya', 'Anjali', 'Kavya', 'Riya', 'Sneha', 'Pooja', 'Shruti', 'Neha'],
      other: ['Alex', 'Sam', 'Riley', 'Jordan', 'Casey', 'Taylor', 'Morgan', 'Avery']
    };
    
    const suggestions = nameSuggestions[gender] || nameSuggestions.other;
    return suggestions.slice(0, 6); // Show 6 suggestions
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));

    // Generate suggestions when gender changes
    if (name === 'gender' && value) {
      setSuggestedNames(generateSuggestedNames(value));
    }
  };

  const handleSuggestedNameClick = (name) => {
    setFormData(prev => ({
      ...prev,
      name: name
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!formData.name.trim()) {
      setMessage({ type: 'error', text: 'Name is required' });
      return;
    }
    
    if (!formData.gender) {
      setMessage({ type: 'error', text: 'Please select your gender' });
      return;
    }

    setLoading(true);
    setMessage('');

    try {
      const response = await axios.post(`${apiBaseUrl}/api/register/step1`, {
        phone,
        name: formData.name.trim(),
        gender: formData.gender
      });

      // Login the user with the returned data
      login(response.data.user, response.data.access_token);
      
      setMessage({ type: 'success', text: 'Registration completed! You can now access the Call panel.' });
      
      // Close modal after a short delay
      setTimeout(() => {
        onSuccess && onSuccess(response.data.user);
        onClose();
      }, 1500);

    } catch (error) {
      const errorMessage = error.response?.data?.error || 'Registration failed. Please try again.';
      setMessage({ type: 'error', text: errorMessage });
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
    }
    return () => {
      document.body.style.overflow = 'unset';
    };
  }, [isOpen]);

  if (!isOpen) return null;

  const modalContent = (
    <div 
      style={{
        position: 'fixed',
        top: '0',
        left: '0',
        right: '0',
        bottom: '0',
        backgroundColor: 'rgba(0, 0, 0, 0.75)',
        zIndex: '99999',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        padding: '20px',
        overflow: 'auto'
      }}
      onClick={(e) => e.target === e.currentTarget && onClose()}
    >
      <div 
        style={{
          backgroundColor: '#ffffff',
          borderRadius: '20px',
          width: '100%',
          maxWidth: '450px',
          maxHeight: '85vh',
          overflowY: 'auto',
          boxShadow: '0 25px 50px rgba(0, 0, 0, 0.25)',
          position: 'relative',
          margin: 'auto',
          transform: 'translateZ(0)'
        }}
        onClick={(e) => e.stopPropagation()}
      >
        <div className="modal-header">
          <h2>Complete Your Profile</h2>
          <p className="modal-subtitle">Step 1 of 3: Basic Information</p>
        </div>

        <form onSubmit={handleSubmit} className="step-form">
          <div className="form-group">
            <label htmlFor="gender" className="form-label">Gender *</label>
            <select
              id="gender"
              name="gender"
              value={formData.gender}
              onChange={handleInputChange}
              className="form-select"
              required
            >
              <option value="">Select Gender</option>
              <option value="male">Male</option>
              <option value="female">Female</option>
              <option value="other">Other</option>
            </select>
          </div>

          <div className="form-group">
            <label htmlFor="name" className="form-label">Name *</label>
            <input
              type="text"
              id="name"
              name="name"
              value={formData.name}
              onChange={handleInputChange}
              className="form-input"
              placeholder="Enter your name"
              required
            />
            
            {formData.gender && suggestedNames.length > 0 && (
              <div className="suggested-names">
                <p className="suggestions-label">Suggested names:</p>
                <div className="suggestions-grid">
                  {suggestedNames.map((name, index) => (
                    <button
                      key={index}
                      type="button"
                      className={`suggestion-btn ${formData.name === name ? 'selected' : ''}`}
                      onClick={() => handleSuggestedNameClick(name)}
                    >
                      {name}
                    </button>
                  ))}
                </div>
              </div>
            )}
          </div>

          {message && (
            <div className={`message ${message.type}`}>
              {message.text}
            </div>
          )}

          <div className="modal-actions">
            <button
              type="button"
              className="secondary-btn"
              onClick={onClose}
              disabled={loading}
            >
              Cancel
            </button>
            <button
              type="submit"
              className="primary-btn"
              disabled={loading || !formData.name.trim() || !formData.gender}
            >
              {loading ? 'Completing...' : 'Complete & Continue'}
            </button>
          </div>
        </form>

        <div className="step-progress">
          <div className="progress-dots">
            <div className="dot active"></div>
            <div className="dot"></div>
            <div className="dot"></div>
          </div>
          <p className="progress-text">Access to Call panel will be unlocked</p>
        </div>
      </div>
    </div>
  );

  return ReactDOM.createPortal(modalContent, document.body);
};

export default Step1RegistrationModal;
