import React, { useState, useEffect } from 'react';
import ReactDOM from 'react-dom';
import { useAuth } from '../context/AuthContext';
import axios from 'axios';
import { apiBaseUrl } from '../config';

const Step3RegistrationModal = ({ isOpen, onClose, onSuccess }) => {
  const { updateUser, token } = useAuth();
  const [selectedInterests, setSelectedInterests] = useState([]);
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');

  const availableInterests = [
    'Fashion', 'Sports', 'Technology', 'Entertainment', 'Travel', 'Food & Cooking',
    'Music', 'Movies', 'Books', 'Art & Design', 'Photography', 'Fitness & Health',
    'Gaming', 'Business', 'Education', 'Nature', 'Dancing', 'Writing', 'Crafting',
    'Fashion Design', 'Beauty', 'Automotive', 'Finance', 'Real Estate', 'Pets'
  ];

  const handleInterestToggle = (interest) => {
    setSelectedInterests(prev => {
      if (prev.includes(interest)) {
        return prev.filter(item => item !== interest);
      } else {
        return [...prev, interest];
      }
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (selectedInterests.length === 0) {
      setMessage({ type: 'error', text: 'Please select at least one interest' });
      return;
    }

    setLoading(true);
    setMessage('');

    try {
      const response = await axios.post(`${apiBaseUrl}/api/register/step3`, {
        interests: selectedInterests
      }, {
        headers: { Authorization: `Bearer ${token}` }
      });

      // Update user data in context
      updateUser(response.data.user);
      
      setMessage({ type: 'success', text: 'Interests saved! You can now access the Ads panel.' });
      
      // Close modal after a short delay
      setTimeout(() => {
        onSuccess && onSuccess(response.data.user);
        onClose();
      }, 1500);

    } catch (error) {
      const errorMessage = error.response?.data?.error || 'Failed to save interests. Please try again.';
      setMessage({ type: 'error', text: errorMessage });
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
    }
    return () => {
      document.body.style.overflow = 'unset';
    };
  }, [isOpen]);

  if (!isOpen) return null;

  const modalContent = (
    <div 
      style={{
        position: 'fixed',
        top: '0',
        left: '0',
        right: '0',
        bottom: '0',
        backgroundColor: 'rgba(0, 0, 0, 0.75)',
        zIndex: '99999',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        padding: '20px',
        overflow: 'auto'
      }}
      onClick={(e) => e.target === e.currentTarget && onClose()}
    >
      <div 
        style={{
          backgroundColor: '#ffffff',
          borderRadius: '20px',
          width: '100%',
          maxWidth: '450px',
          maxHeight: '85vh',
          overflowY: 'auto',
          boxShadow: '0 25px 50px rgba(0, 0, 0, 0.25)',
          position: 'relative',
          margin: 'auto',
          transform: 'translateZ(0)'
        }}
        onClick={(e) => e.stopPropagation()}
      >
        <div className="modal-header">
          <h2>Select Your Interests</h2>
          <p className="modal-subtitle">Step 3 of 3: Interest Selection</p>
        </div>

        <form onSubmit={handleSubmit} className="step-form">
          <div className="form-group">
            <label className="form-label">Choose your interests *</label>
            <p className="form-help-text">Select all that apply to personalize your experience</p>
            
            <div className="interests-grid">
              {availableInterests.map((interest, index) => (
                <button
                  key={index}
                  type="button"
                  className={`interest-chip ${selectedInterests.includes(interest) ? 'selected' : ''}`}
                  onClick={() => handleInterestToggle(interest)}
                >
                  {interest}
                </button>
              ))}
            </div>
            
            <div className="selected-count">
              {selectedInterests.length} interest{selectedInterests.length !== 1 ? 's' : ''} selected
            </div>
          </div>

          {message && (
            <div className={`message ${message.type}`}>
              {message.text}
            </div>
          )}

          <div className="modal-actions">
            <button
              type="button"
              className="secondary-btn"
              onClick={onClose}
              disabled={loading}
            >
              Cancel
            </button>
            <button
              type="submit"
              className="primary-btn"
              disabled={loading || selectedInterests.length === 0}
            >
              {loading ? 'Saving...' : 'Save & Continue'}
            </button>
          </div>
        </form>

        <div className="step-progress">
          <div className="progress-dots">
            <div className="dot completed"></div>
            <div className="dot completed"></div>
            <div className="dot active"></div>
          </div>
          <p className="progress-text">Access to Ads panel will be unlocked</p>
        </div>
      </div>
    </div>
  );

  return ReactDOM.createPortal(modalContent, document.body);
};

export default Step3RegistrationModal;
