import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { apiBaseUrl } from '../config';
import ProviderImageUpload from './ProviderImageUpload';
import ProviderVariantsManager from './ProviderVariantsManager';

const ProviderDashboard = () => {
  const navigate = useNavigate();
  const [products, setProducts] = useState([]);
  const [categories, setCategories] = useState([]);
  const [loading, setLoading] = useState(true);
  const [showAddProductModal, setShowAddProductModal] = useState(false);
  const [editingProduct, setEditingProduct] = useState(null);
  const [showVariantsModal, setShowVariantsModal] = useState(false);
  const [selectedProduct, setSelectedProduct] = useState(null);
  const [productForm, setProductForm] = useState({
    title: '',
    description: '',
    stock_quantity: '',
    original_price: '',
    discounted_price: '',
    category_id: '',
    subcategory_id: '',
    images: []
  });
  const [subcategories, setSubcategories] = useState([]);

  useEffect(() => {
    fetchProducts();
    fetchCategories();
  }, []);

  useEffect(() => {
    if (productForm.category_id) {
      fetchSubcategories(productForm.category_id);
    } else {
      setSubcategories([]);
      setProductForm(prev => ({ ...prev, subcategory_id: '' }));
    }
  }, [productForm.category_id]);

  const fetchProducts = async () => {
    try {
      const token = localStorage.getItem('authToken');
      const response = await fetch(`${apiBaseUrl}/api/provider/products`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      if (response.ok) {
        const data = await response.json();
        setProducts(data.products || []);
      }
    } catch (error) {
      console.error('Error fetching products:', error);
    } finally {
      setLoading(false);
    }
  };

  const fetchCategories = async () => {
    try {
      const response = await fetch(`${apiBaseUrl}/api/categories`);
      if (response.ok) {
        const data = await response.json();
        setCategories(data.categories || []);
      }
    } catch (error) {
      console.error('Error fetching categories:', error);
    }
  };

  const fetchSubcategories = async (categoryId) => {
    try {
      const response = await fetch(`${apiBaseUrl}/api/subcategories?category_id=${categoryId}`);
      if (response.ok) {
        const data = await response.json();
        setSubcategories(data.subcategories || []);
      }
    } catch (e) {
      console.error('Error fetching subcategories', e);
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const token = localStorage.getItem('authToken');
      const url = editingProduct 
        ? `${apiBaseUrl}/api/provider/products/${editingProduct.id}`
        : `${apiBaseUrl}/api/provider/products`;
      
      const method = editingProduct ? 'PUT' : 'POST';
      
      const requestData = {
        ...productForm,
        stock_quantity: parseInt(productForm.stock_quantity),
        original_price: parseFloat(productForm.original_price),
        discounted_price: productForm.discounted_price ? parseFloat(productForm.discounted_price) : null,
        category_id: parseInt(productForm.category_id),
        subcategory_id: productForm.subcategory_id ? parseInt(productForm.subcategory_id) : null,
        images: (productForm.images || []).map(i => i.url)
      };
      
      console.log('Submitting product:', requestData);
      console.log('API URL:', url);
      console.log('Method:', method);
      console.log('Token available:', token ? 'Yes' : 'No');
      
      const response = await fetch(url, {
        method,
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(requestData)
      });
      
      console.log('Response status:', response.status);

      if (response.ok) {
        const result = await response.json();
        console.log('Product submission success:', result);
        
        // Show appropriate message based on response
        if (result.provider_application_created) {
          alert(result.message);
        } else {
          alert(editingProduct ? 'Product updated successfully!' : 'Product added successfully!');
        }
        
        setShowAddProductModal(false);
        setEditingProduct(null);
        setProductForm({
          title: '',
          description: '',
          stock_quantity: '',
          original_price: '',
          discounted_price: '',
          category_id: '',
          subcategory_id: '',
          images: []
        });
        fetchProducts();
      } else {
        const error = await response.json();
        console.log('Product submission error:', error);
        alert(error.error || 'Failed to save product');
      }
    } catch (error) {
      console.error('Error saving product:', error);
      alert('Failed to save product');
    }
  };

  const handleEdit = (product) => {
    setEditingProduct(product);
    setProductForm({
      title: product.title || '',
      description: product.description || '',
      stock_quantity: product.stock_quantity ? product.stock_quantity.toString() : '',
      original_price: product.original_price ? product.original_price.toString() : '',
      discounted_price: product.discounted_price ? product.discounted_price.toString() : '',
      category_id: product.category_id ? product.category_id.toString() : '',
      subcategory_id: product.subcategory_id ? String(product.subcategory_id) : '',
      images: (product.images || []).map(url => ({ url, filename: url.split('/').pop(), size: 0 }))
    });
    setShowAddProductModal(true);
  };

  const handleDelete = async (productId) => {
    if (!window.confirm('Are you sure you want to delete this product?')) {
      return;
    }

    try {
      const token = localStorage.getItem('authToken');
      console.log('Deleting product ID:', productId); // Debug log
      console.log('Token available:', token ? 'Yes' : 'No'); // Debug log
      
      const response = await fetch(`${apiBaseUrl}/api/provider/products/${productId}`, {
        method: 'DELETE',
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      console.log('Delete response status:', response.status); // Debug log

      if (response.ok) {
        const result = await response.json();
        console.log('Delete success:', result); // Debug log
        alert('Product deleted successfully!');
        fetchProducts();
      } else {
        const error = await response.json();
        console.log('Delete error:', error); // Debug log
        alert(error.error || 'Failed to delete product');
      }
    } catch (error) {
      console.error('Error deleting product:', error);
      alert('Failed to delete product');
    }
  };

  if (loading) {
    return (
      <div className="loading-container">
        <div className="loading-spinner"></div>
        <p>Loading your products...</p>
      </div>
    );
  }

  return (
    <div className="provider-dashboard page-with-bottom-nav">
      <div className="dashboard-header">
        <h1>My Products</h1>
        <button 
          className="btn-primary"
          onClick={() => {
            setEditingProduct(null);
            setProductForm({
              title: '',
              description: '',
              stock_quantity: '',
              original_price: '',
              discounted_price: '',
              category_id: '',
              subcategory_id: '',
              images: []
            });
            setShowAddProductModal(true);
          }}
        >
          + Add New Product
        </button>
      </div>

      <div className="products-grid">
        {products.length === 0 ? (
          <div className="no-products">
            <p>No products found. Add your first product to get started!</p>
          </div>
        ) : (
          products.map(product => (
            <div key={product.id} className="product-card">
              <div className="product-image">
                {product.images ? (
                  <img 
                    src={JSON.parse(product.images)[0]} 
                    alt={product.title}
                    onError={(e) => {
                      e.target.src = 'https://via.placeholder.com/300x200?text=No+Image';
                    }}
                  />
                ) : (
                  <div className="no-image">No Image</div>
                )}
              </div>
              <div className="product-info">
                <div className="product-header">
                  <h3>{product.title}</h3>
                  <span className={`status-badge ${product.approval_status}`}>
                    {product.approval_status === 'pending' ? 'Under Review' : 
                     product.approval_status === 'approved' ? 'Approved' : 'Rejected'}
                  </span>
                </div>
                <p className="product-description">{product.description}</p>
                <div className="product-meta">
                  <span className="category">Category: {product.category_name}</span>
                  <span className="stock">Stock: {product.stock_quantity}</span>
                </div>
                <div className="product-pricing">
                  <span className="original-price">{product.original_price}</span>
                  <span className="discounted-price">{product.discounted_price}</span>
                </div>
                <div className="product-actions">
                  <button 
                    className="btn-edit"
                    onClick={() => handleEdit(product)}
                  >
                    Edit
                  </button>
                  <button 
                    className="btn-primary"
                    onClick={() => { setSelectedProduct(product); setShowVariantsModal(true); }}
                  >
                    Manage Variants
                  </button>
                  <button 
                    className="btn-delete"
                    onClick={() => handleDelete(product.id)}
                  >
                    Delete
                  </button>
                </div>
              </div>
            </div>
          ))
        )}
      </div>

      {/* Add/Edit Product Modal */}
      {showAddProductModal && (
        <div className="modal-overlay" onClick={() => setShowAddProductModal(false)}>
          <div className="modal-content" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h3>{editingProduct ? 'Edit Product' : 'Add New Product'}</h3>
              <button className="close-modal" onClick={() => setShowAddProductModal(false)}></button>
            </div>
            <form onSubmit={handleSubmit} className="modal-body">
              <div className="form-group">
                <label>Product Title *</label>
                <input
                  type="text"
                  value={productForm.title}
                  onChange={(e) => setProductForm({...productForm, title: e.target.value})}
                  placeholder="Enter product title"
                  required
                />
              </div>
              <div className="form-group">
                <label>Description *</label>
                <textarea
                  value={productForm.description}
                  onChange={(e) => setProductForm({...productForm, description: e.target.value})}
                  placeholder="Enter product description"
                  required
                />
              </div>
              <div className="form-row">
                <div className="form-group">
                  <label>Stock Quantity *</label>
                  <input
                    type="number"
                    value={productForm.stock_quantity}
                    onChange={(e) => setProductForm({...productForm, stock_quantity: e.target.value})}
                    placeholder="Enter stock quantity"
                    min="0"
                    required
                  />
                </div>
                <div className="form-group">
                  <label>Category *</label>
                  <select
                    value={productForm.category_id}
                    onChange={(e) => setProductForm({...productForm, category_id: e.target.value})}
                    required
                  >
                    <option value="">Select Category</option>
                    {categories.map(category => (
                      <option key={category.id} value={category.id}>
                        {category.name}
                      </option>
                    ))}
                  </select>
                </div>
              </div>
              {productForm.category_id && (
                <div className="form-group">
                  <label>Subcategory</label>
                  <select
                    value={productForm.subcategory_id}
                    onChange={(e) => setProductForm({ ...productForm, subcategory_id: e.target.value })}
                  >
                    <option value="">Select Subcategory (Optional)</option>
                    {subcategories.map(sc => (
                      <option key={sc.id} value={sc.id}>{sc.name}</option>
                    ))}
                  </select>
                </div>
              )}
              <div className="form-row">
                <div className="form-group">
                  <label>Original Price *</label>
                  <input
                    type="number"
                    value={productForm.original_price}
                    onChange={(e) => setProductForm({...productForm, original_price: e.target.value})}
                    placeholder="Enter original price"
                    min="0"
                    step="0.01"
                    required
                  />
                </div>
                <div className="form-group">
                  <label>Discounted Price *</label>
                  <input
                    type="number"
                    value={productForm.discounted_price}
                    onChange={(e) => setProductForm({...productForm, discounted_price: e.target.value})}
                    placeholder="Enter discounted price"
                    min="0"
                    step="0.01"
                    required
                  />
                </div>
              </div>
              <div className="form-group">
                <ProviderImageUpload
                  images={productForm.images}
                  onImagesChange={(imgs) => setProductForm({ ...productForm, images: imgs })}
                  maxImages={5}
                />
              </div>
              <div className="modal-footer">
                <button type="button" className="btn-secondary" onClick={() => setShowAddProductModal(false)}>
                  Cancel
                </button>
                <button type="submit" className="btn-primary">
                  {editingProduct ? 'Update Product' : 'Add Product'}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
      {showVariantsModal && selectedProduct && (
        <ProviderVariantsManager product={selectedProduct} onClose={() => { setShowVariantsModal(false); setSelectedProduct(null); }} />
      )}
    </div>
  );
};

export default ProviderDashboard;
