import React, { useState, useEffect } from 'react';
import { apiBaseUrl } from '../config';

const SuperAdminPanel = () => {
  const [activeTab, setActiveTab] = useState('admins');
  const [admins, setAdmins] = useState([]);
  const [activities, setActivities] = useState([]);
  const [loading, setLoading] = useState(false);
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [showPermissionsModal, setShowPermissionsModal] = useState(false);
  const [selectedAdmin, setSelectedAdmin] = useState(null);
  const [permissions, setPermissions] = useState({});
  const [notification, setNotification] = useState(null);
  
  // Form states
  const [newAdmin, setNewAdmin] = useState({
    username: '',
    password: '',
    email: '',
    role: 'admin'
  });
  
  const [permissionForm, setPermissionForm] = useState({});

  const [moduleCategories, setModuleCategories] = useState([]);
  const [allModules, setAllModules] = useState([]); // Flattened list for easy access

  useEffect(() => {
    fetchModules();
    if (activeTab === 'admins') {
      fetchAdmins();
    } else if (activeTab === 'activities') {
      fetchActivities();
    }
  }, [activeTab]);

  const fetchModules = async () => {
    try {
      const token = localStorage.getItem('adminToken');
      const response = await fetch(`${apiBaseUrl}/api/super-admin/modules`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
      
      if (response.ok) {
        const data = await response.json();
        setModuleCategories(data.module_categories || []);
        
        // Flatten modules for easy access
        const flatModules = [];
        data.module_categories?.forEach(category => {
          category.modules?.forEach(module => {
            flatModules.push(module);
          });
        });
        setAllModules(flatModules);
      } else {
        console.error('Failed to fetch modules');
      }
    } catch (error) {
      console.error('Error fetching modules:', error);
    }
  };

  const fetchAdmins = async () => {
    setLoading(true);
    try {
      const token = localStorage.getItem('adminToken');
      const response = await fetch(`${apiBaseUrl}/api/super-admin/admin-permissions`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
      
      if (response.ok) {
        const data = await response.json();
        setAdmins(data.admins);
      } else {
        console.error('Failed to fetch admins');
      }
    } catch (error) {
      console.error('Error fetching admins:', error);
    } finally {
      setLoading(false);
    }
  };

  const fetchActivities = async () => {
    setLoading(true);
    try {
      const token = localStorage.getItem('adminToken');
      const response = await fetch(`${apiBaseUrl}/api/admin/super-admin/activity-log`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
      
      if (response.ok) {
        const data = await response.json();
        setActivities(data.activities);
      } else {
        console.error('Failed to fetch activities');
      }
    } catch (error) {
      console.error('Error fetching activities:', error);
    } finally {
      setLoading(false);
    }
  };

  const fetchAdminPermissions = async (adminId) => {
    try {
      // Since we already have the admin data with permissions from fetchAdmins,
      // we can extract the permissions directly from the admins array
      const admin = admins.find(a => a.id === adminId);
      if (admin && admin.permissions) {
        setPermissions(admin.permissions);
        return admin.permissions;
      }
      return {};
    } catch (error) {
      console.error('Error fetching permissions:', error);
      return {};
    }
  };

  const handleCreateAdmin = async (e) => {
    e.preventDefault();
    setLoading(true);
    
    try {
      const token = localStorage.getItem('adminToken');
      const response = await fetch(`${apiBaseUrl}/api/admin/super-admin/admins`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(newAdmin)
      });
      
      if (response.ok) {
        setShowCreateModal(false);
        setNewAdmin({ username: '', password: '', email: '', role: 'admin' });
        setNotification({ type: 'success', message: 'Admin created successfully!' });
        setTimeout(() => setNotification(null), 3000);
        // Secret refresh
        fetchAdmins();
      } else {
        const error = await response.json();
        setNotification({ type: 'error', message: error.error || 'Error creating admin' });
        setTimeout(() => setNotification(null), 3000);
      }
    } catch (error) {
      console.error('Error creating admin:', error);
      setNotification({ type: 'error', message: 'Error creating admin' });
      setTimeout(() => setNotification(null), 3000);
    } finally {
      setLoading(false);
    }
  };

  const handleUpdatePermissions = async (e) => {
    e.preventDefault();
    setLoading(true);
    
    try {
      const token = localStorage.getItem('adminToken');
      
      const response = await fetch(`${apiBaseUrl}/api/super-admin/admin-permissions/${selectedAdmin.id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({ permissions: permissionForm })
      });
      
      if (response.ok) {
        setShowPermissionsModal(false);
        setSelectedAdmin(null);
        setPermissionForm({});
        setNotification({ type: 'success', message: 'Permissions updated successfully!' });
        setTimeout(() => setNotification(null), 3000);
        // Secret refresh
        fetchAdmins();
      } else {
        const error = await response.json();
        setNotification({ type: 'error', message: error.error || 'Error updating permissions' });
        setTimeout(() => setNotification(null), 3000);
      }
    } catch (error) {
      console.error('Error updating permissions:', error);
      setNotification({ type: 'error', message: 'Error updating permissions' });
      setTimeout(() => setNotification(null), 3000);
    } finally {
      setLoading(false);
    }
  };

  const handleDeleteAdmin = async (adminId) => {
    if (!window.confirm('Are you sure you want to delete this admin?')) {
      return;
    }
    
    setLoading(true);
    try {
      const token = localStorage.getItem('adminToken');
      const response = await fetch(`${apiBaseUrl}/api/admin/super-admin/admins/${adminId}`, {
        method: 'DELETE',
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
      
      if (response.ok) {
        setNotification({ type: 'success', message: 'Admin deleted successfully!' });
        setTimeout(() => setNotification(null), 3000);
        // Secret refresh
        fetchAdmins();
      } else {
        const error = await response.json();
        setNotification({ type: 'error', message: error.error || 'Error deleting admin' });
        setTimeout(() => setNotification(null), 3000);
      }
    } catch (error) {
      console.error('Error deleting admin:', error);
      setNotification({ type: 'error', message: 'Error deleting admin' });
      setTimeout(() => setNotification(null), 3000);
    } finally {
      setLoading(false);
    }
  };

  const openPermissionsModal = async (admin) => {
    setSelectedAdmin(admin);
    const adminPermissions = await fetchAdminPermissions(admin.id);
    
    // Initialize permission form with current permissions
    const formData = {};
    allModules.forEach(module => {
      const existingPerm = adminPermissions[module.key];
      formData[module.key] = {
        can_view: existingPerm?.can_view || false,
        can_create: existingPerm?.can_create || false,
        can_edit: existingPerm?.can_edit || false,
        can_delete: existingPerm?.can_delete || false
      };
    });
    
    setPermissionForm(formData);
    setShowPermissionsModal(true);
  };

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleString();
  };

  return (
    <div className="super-admin-panel">
      {/* Notification */}
      {notification && (
        <div className={`notification ${notification.type}`} style={{
          position: 'fixed',
          top: '20px',
          right: '20px',
          padding: '15px 20px',
          borderRadius: '8px',
          backgroundColor: notification.type === 'success' ? '#10b981' : '#ef4444',
          color: 'white',
          boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
          zIndex: 9999,
          animation: 'slideIn 0.3s ease-out'
        }}>
          {notification.message}
        </div>
      )}
      
      <div className="admin-header">
        <h1>Super Admin Panel</h1>
        <div className="admin-tabs">
          <button 
            className={activeTab === 'admins' ? 'active' : ''}
            onClick={() => setActiveTab('admins')}
          >
            Manage Admins
          </button>
          <button 
            className={activeTab === 'activities' ? 'active' : ''}
            onClick={() => setActiveTab('activities')}
          >
            Activity Log
          </button>
        </div>
      </div>

      {activeTab === 'admins' && (
        <div className="admins-section">
          <div className="section-header">
            <h2>Admin Management</h2>
            <button 
              className="btn btn-primary"
              onClick={() => setShowCreateModal(true)}
            >
              Create New Admin
            </button>
          </div>

          {loading ? (
            <div className="loading">Loading admins...</div>
          ) : (
            <div className="admins-table">
              <table>
                <thead>
                  <tr>
                    <th>ID</th>
                    <th>Username</th>
                    <th>Email</th>
                    <th>Role</th>
                    <th>Created By</th>
                    <th>Last Login</th>
                    <th>Status</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>
                  {admins.map(admin => (
                    <tr key={admin.id}>
                      <td>{admin.id}</td>
                      <td>{admin.username}</td>
                      <td>{admin.email}</td>
                      <td>
                        <span className={`role-badge ${admin.role}`}>
                          {admin.role}
                        </span>
                      </td>
                      <td>{admin.created_by}</td>
                      <td>{admin.last_login ? formatDate(admin.last_login) : 'Never'}</td>
                      <td>
                        <span className={`status-badge ${admin.is_active ? 'active' : 'inactive'}`}>
                          {admin.is_active ? 'Active' : 'Inactive'}
                        </span>
                      </td>
                      <td>
                        <div className="action-buttons">
                          <button 
                            className="btn btn-sm btn-secondary"
                            onClick={() => openPermissionsModal(admin)}
                          >
                            Permissions
                          </button>
                          {admin.role !== 'super_admin' && (
                            <button 
                              className="btn btn-sm btn-danger"
                              onClick={() => handleDeleteAdmin(admin.id)}
                            >
                              Delete
                            </button>
                          )}
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>
      )}

      {activeTab === 'activities' && (
        <div className="activities-section">
          <div className="section-header">
            <h2>Activity Log</h2>
            <button 
              className="btn btn-secondary"
              onClick={fetchActivities}
            >
              Refresh
            </button>
          </div>

          {loading ? (
            <div className="loading">Loading activities...</div>
          ) : (
            <div className="activities-list">
              {activities.map(activity => (
                <div key={activity.id} className="activity-item">
                  <div className="activity-header">
                    <span className="admin-name">{activity.admin_username}</span>
                    <span className="activity-time">{formatDate(activity.created_at)}</span>
                  </div>
                  <div className="activity-details">
                    <span className="action">{activity.action}</span>
                    <span className="module">{activity.module}</span>
                    {activity.details && (
                      <span className="details">{activity.details}</span>
                    )}
                  </div>
                  <div className="activity-meta">
                    <span className="ip">IP: {activity.ip_address}</span>
                    {activity.target_id && (
                      <span className="target">Target ID: {activity.target_id}</span>
                    )}
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      )}

      {/* Create Admin Modal */}
      {showCreateModal && (
        <div className="modal-overlay">
          <div className="modal">
            <div className="modal-header">
              <h3>Create New Admin</h3>
              <button 
                className="close-btn"
                onClick={() => setShowCreateModal(false)}
              >
                
              </button>
            </div>
            <form onSubmit={handleCreateAdmin}>
              <div className="form-group">
                <label>Username:</label>
                <input
                  type="text"
                  value={newAdmin.username}
                  onChange={(e) => setNewAdmin({...newAdmin, username: e.target.value})}
                  required
                />
              </div>
              <div className="form-group">
                <label>Password:</label>
                <input
                  type="password"
                  value={newAdmin.password}
                  onChange={(e) => setNewAdmin({...newAdmin, password: e.target.value})}
                  required
                />
              </div>
              <div className="form-group">
                <label>Email:</label>
                <input
                  type="email"
                  value={newAdmin.email}
                  onChange={(e) => setNewAdmin({...newAdmin, email: e.target.value})}
                />
              </div>
              <div className="form-group">
                <label>Role:</label>
                <select
                  value={newAdmin.role}
                  onChange={(e) => setNewAdmin({...newAdmin, role: e.target.value})}
                >
                  <option value="admin">Admin</option>
                </select>
                <small style={{color: '#6c757d', fontSize: '0.8rem'}}>
                  Only one Super Admin is allowed (admin/admin123)
                </small>
              </div>
              <div className="modal-actions">
                <button type="button" onClick={() => setShowCreateModal(false)}>
                  Cancel
                </button>
                <button type="submit" disabled={loading}>
                  {loading ? 'Creating...' : 'Create Admin'}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      {/* Permissions Modal */}
      {showPermissionsModal && selectedAdmin && (
        <div className="modal-overlay">
          <div className="modal permissions-modal">
            <div className="modal-header">
              <h3>Manage Permissions - {selectedAdmin.username}</h3>
              <button 
                className="close-btn"
                onClick={() => setShowPermissionsModal(false)}
              >
                
              </button>
            </div>
            <form onSubmit={handleUpdatePermissions}>
              <div className="permissions-container" style={{ maxHeight: '60vh', overflowY: 'auto' }}>
                {moduleCategories.map((category, categoryIndex) => (
                  <div key={categoryIndex} className="permission-category" style={{
                    marginBottom: '25px',
                    backgroundColor: '#f9fafb',
                    padding: '20px',
                    borderRadius: '12px',
                    border: '1px solid #e5e7eb'
                  }}>
                    <div style={{ marginBottom: '15px' }}>
                      <h3 style={{ 
                        margin: '0 0 5px 0', 
                        color: '#111827',
                        fontSize: '18px',
                        fontWeight: '600'
                      }}>
                        {category.category}
                      </h3>
                      <p style={{ 
                        margin: 0, 
                        fontSize: '13px', 
                        color: '#6b7280'
                      }}>
                        {category.description}
                      </p>
                    </div>
                    
                    <div className="permissions-grid" style={{
                      display: 'grid',
                      gap: '12px'
                    }}>
                      {category.modules?.map(module => {
                        const modulePerms = permissionForm[module.key] || {};
                        const hasFullAccess = modulePerms.can_view && modulePerms.can_create && 
                                             modulePerms.can_edit && modulePerms.can_delete;
                        
                        return (
                          <div key={module.key} className="permission-module" style={{
                            padding: '15px',
                            border: hasFullAccess ? '2px solid #10b981' : '2px solid #e5e7eb',
                            borderRadius: '8px',
                            backgroundColor: hasFullAccess ? '#f0fdf4' : '#ffffff',
                            transition: 'all 0.2s',
                            boxShadow: '0 1px 2px 0 rgba(0, 0, 0, 0.05)'
                          }}>
                            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start' }}>
                              <div style={{ flex: 1 }}>
                                <h4 style={{ margin: '0 0 5px 0', fontSize: '15px', fontWeight: '500' }}>{module.name}</h4>
                                {module.description && (
                                  <p className="module-description" style={{ 
                                    fontSize: '12px', 
                                    color: '#6b7280', 
                                    margin: '0',
                                    fontWeight: '400'
                                  }}>
                                    {module.description}
                                  </p>
                                )}
                              </div>
                              <label className="permission-checkbox" style={{ 
                                fontWeight: '600', 
                                color: hasFullAccess ? '#10b981' : '#6b7280',
                                cursor: 'pointer',
                                display: 'flex',
                                alignItems: 'center',
                                gap: '8px',
                                fontSize: '14px',
                                marginLeft: '15px',
                                whiteSpace: 'nowrap'
                              }}>
                                <input
                                  type="checkbox"
                                  checked={hasFullAccess}
                                  onChange={(e) => {
                                    const allPerms = e.target.checked;
                                    setPermissionForm({
                                      ...permissionForm,
                                      [module.key]: {
                                        can_view: allPerms,
                                        can_create: allPerms,
                                        can_edit: allPerms,
                                        can_delete: allPerms
                                      }
                                    });
                                  }}
                                  style={{
                                    width: '20px',
                                    height: '20px',
                                    cursor: 'pointer'
                                  }}
                                />
                                {hasFullAccess ? ' Enabled' : 'Enable'}
                              </label>
                            </div>
                          </div>
                        );
                      })}
                    </div>
                  </div>
                ))}
              </div>
              <div className="modal-actions">
                <button type="button" onClick={() => setShowPermissionsModal(false)}>
                  Cancel
                </button>
                <button type="submit" disabled={loading}>
                  {loading ? 'Updating...' : 'Update Permissions'}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
};

export default SuperAdminPanel;
