import React, { useState, useEffect } from 'react';
import { apiBaseUrl } from '../config';

const AdminOrdersPanel = () => {
  const [orders, setOrders] = useState([]);
  const [loading, setLoading] = useState(false);
  const [activeTab, setActiveTab] = useState('requested');
  const [selectedOrder, setSelectedOrder] = useState(null);
  const [showDetailsModal, setShowDetailsModal] = useState(false);
  const [adminNotes, setAdminNotes] = useState('');
  const [notification, setNotification] = useState(null);

  useEffect(() => {
    fetchOrders(activeTab);
  }, [activeTab]);

  const fetchOrders = async (status) => {
    try {
      setLoading(true);
      const token = localStorage.getItem('adminToken');
      const response = await fetch(`${apiBaseUrl}/api/admin/orders?status=${status}`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });
      const data = await response.json();
      setOrders(data.orders || []);
    } catch (error) {
      console.error('Error fetching orders:', error);
    } finally {
      setLoading(false);
    }
  };

  const updateOrderStatus = async (orderId, newStatus) => {
    try {
      const token = localStorage.getItem('adminToken');
      const response = await fetch(`${apiBaseUrl}/api/admin/orders/${orderId}/status`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({
          status: newStatus,
          admin_notes: adminNotes
        })
      });
      
      if (response.ok) {
        setShowDetailsModal(false);
        setSelectedOrder(null);
        setAdminNotes('');
        setNotification({ type: 'success', message: 'Order status updated successfully' });
        setTimeout(() => setNotification(null), 3000);
        // Secret refresh
        fetchOrders(activeTab);
      } else {
        const error = await response.json();
        setNotification({ type: 'error', message: error.error || 'Failed to update order status' });
        setTimeout(() => setNotification(null), 3000);
      }
    } catch (error) {
      console.error('Error updating order status:', error);
      setNotification({ type: 'error', message: 'Failed to update order status' });
      setTimeout(() => setNotification(null), 3000);
    }
  };

  const getNextStatus = (currentStatus) => {
    const statusFlow = {
      'requested': 'shipment',
      'shipment': 'completed',
      'completed': null
    };
    return statusFlow[currentStatus];
  };

  const getStatusLabel = (status) => {
    const labels = {
      'requested': 'Requested',
      'shipment': 'In Shipment',
      'completed': 'Completed'
    };
    return labels[status] || status;
  };

  return (
    <div className="admin-orders-panel">
      {/* Notification */}
      {notification && (
        <div className={`notification ${notification.type}`} style={{
          position: 'fixed',
          top: '20px',
          right: '20px',
          padding: '15px 20px',
          borderRadius: '8px',
          backgroundColor: notification.type === 'success' ? '#10b981' : '#ef4444',
          color: 'white',
          boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
          zIndex: 9999,
          animation: 'slideIn 0.3s ease-out'
        }}>
          {notification.message}
        </div>
      )}
      
      <div className="panel-header">
        <h2>Admin Orders Management</h2>
      </div>

      <div className="orders-tabs">
        <button
          className={`tab-btn ${activeTab === 'requested' ? 'active' : ''}`}
          onClick={() => setActiveTab('requested')}
        >
          Requested Orders ({orders.filter(o => o.status === 'requested').length || 0})
        </button>
        <button
          className={`tab-btn ${activeTab === 'shipment' ? 'active' : ''}`}
          onClick={() => setActiveTab('shipment')}
        >
          Shipment Orders ({orders.filter(o => o.status === 'shipment').length || 0})
        </button>
        <button
          className={`tab-btn ${activeTab === 'completed' ? 'active' : ''}`}
          onClick={() => setActiveTab('completed')}
        >
          Completed Orders ({orders.filter(o => o.status === 'completed').length || 0})
        </button>
      </div>

      {loading ? (
        <div>Loading...</div>
      ) : (
        <div className="orders-list">
          {orders.length === 0 ? (
            <div className="empty-state">No orders in this category</div>
          ) : (
            <table className="orders-table">
              <thead>
                <tr>
                  <th>Order #</th>
                  <th>Customer</th>
                  <th>Product</th>
                  <th>Quantity</th>
                  <th>Amount</th>
                  <th>Status</th>
                  <th>Date</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                {orders.map(order => (
                  <tr key={order.id}>
                    <td>{order.order_number}</td>
                    <td>
                      <div>{order.customer_name}</div>
                      <div className="small-text">{order.customer_phone}</div>
                    </td>
                    <td>{order.product_title}</td>
                    <td>{order.quantity}</td>
                    <td>{order.total_amount}</td>
                    <td>
                      <span className={`status-badge status-${order.status}`}>
                        {getStatusLabel(order.status)}
                      </span>
                    </td>
                    <td>{new Date(order.created_at).toLocaleDateString()}</td>
                    <td>
                      <button
                        className="btn-sm"
                        onClick={() => {
                          setSelectedOrder(order);
                          setShowDetailsModal(true);
                        }}
                      >
                        View Details
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          )}
        </div>
      )}

      {showDetailsModal && selectedOrder && (
        <div className="modal-overlay" onClick={() => setShowDetailsModal(false)}>
          <div className="modal-content order-details-modal" onClick={(e) => e.stopPropagation()}>
            <h3>Order Details</h3>
            <div className="order-details">
              <div className="detail-row">
                <strong>Order Number:</strong>
                <div>{selectedOrder.order_number}</div>
              </div>
              <div className="detail-row">
                <strong>Product:</strong>
                <div>{selectedOrder.product_title}</div>
              </div>
              <div className="detail-row">
                <strong>Quantity:</strong>
                <div>{selectedOrder.quantity}</div>
              </div>
              <div className="detail-row">
                <strong>Total Amount:</strong>
                <div>{selectedOrder.total_amount}</div>
              </div>
              <div className="detail-row">
                <strong>Status:</strong>
                <div>{getStatusLabel(selectedOrder.status)}</div>
              </div>
              <hr />
              <h4>Customer Information</h4>
              <div className="detail-row">
                <strong>Name:</strong>
                <div>{selectedOrder.customer_name}</div>
              </div>
              <div className="detail-row">
                <strong>Phone:</strong>
                <div>{selectedOrder.customer_phone}</div>
              </div>
              {selectedOrder.customer_email && (
                <div className="detail-row">
                  <strong>Email:</strong>
                  <div>{selectedOrder.customer_email}</div>
                </div>
              )}
              <div className="detail-row">
                <strong>Address:</strong>
                <div>{selectedOrder.customer_address}</div>
              </div>
              <div className="detail-row">
                <strong>Pincode:</strong>
                <div>{selectedOrder.customer_pincode}</div>
              </div>
              {selectedOrder.customer_city && (
                <div className="detail-row">
                  <strong>City:</strong>
                  <div>{selectedOrder.customer_city}</div>
                </div>
              )}
              {selectedOrder.customer_state && (
                <div className="detail-row">
                  <strong>State:</strong>
                  <div>{selectedOrder.customer_state}</div>
                </div>
              )}
              {selectedOrder.admin_notes && (
                <>
                  <hr />
                  <div className="detail-row">
                    <strong>Admin Notes:</strong>
                    <p>{selectedOrder.admin_notes}</p>
                  </div>
                </>
              )}
              <hr />
              <div className="status-update-section">
                <label>Admin Notes:</label>
                <textarea
                  value={adminNotes}
                  onChange={(e) => setAdminNotes(e.target.value)}
                  placeholder="Add notes about this order..."
                  rows="3"
                />
                {getNextStatus(selectedOrder.status) && (
                  <button
                    className="btn-primary"
                    onClick={() => updateOrderStatus(selectedOrder.id, getNextStatus(selectedOrder.status))}
                  >
                    Move to {getStatusLabel(getNextStatus(selectedOrder.status))}
                  </button>
                )}
              </div>
            </div>
            <div className="modal-actions">
              <button onClick={() => setShowDetailsModal(false)}>Close</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default AdminOrdersPanel;

