import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { apiBaseUrl } from '../config';

const ShopPage = () => {
  const navigate = useNavigate();
  const [categories, setCategories] = useState([]);
  const [subcategories, setSubcategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchCategories();
  }, []);

  useEffect(() => {
    if (selectedCategory) {
      fetchSubcategories(selectedCategory.id);
    }
  }, [selectedCategory]);

  const fetchCategories = async () => {
    try {
      const response = await fetch(`${apiBaseUrl}/api/categories`);
      if (response.ok) {
        const data = await response.json();
        setCategories(data.categories || []);
        if (data.categories && data.categories.length > 0) {
          setSelectedCategory(data.categories[0]);
        }
      }
    } catch (error) {
      console.error('Error fetching categories:', error);
    } finally {
      setLoading(false);
    }
  };

  const fetchSubcategories = async (categoryId) => {
    try {
      const response = await fetch(`${apiBaseUrl}/api/subcategories?category_id=${categoryId}`);
      if (response.ok) {
        const data = await response.json();
        setSubcategories(data.subcategories || []);
      }
    } catch (error) {
      console.error('Error fetching subcategories:', error);
    }
  };

  const handleCategoryClick = (category) => {
    setSelectedCategory(category);
  };

  const handleSubcategoryClick = (subcategory) => {
    navigate(`/shop/products?category=${selectedCategory.id}&subcategory=${subcategory.id}`);
  };

  if (loading) {
    return (
      <div className="shop-page-new page-with-bottom-nav">
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p>Loading...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="shop-page-new page-with-bottom-nav">
      {/* Header */}
      <div className="shop-header-new">
        <h1>CATEGORIES</h1>
        <div className="header-icons">
          <button className="icon-btn" onClick={() => navigate('/shop/search')}>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
              <circle cx="11" cy="11" r="8"/>
              <path d="m21 21-4.35-4.35"/>
            </svg>
          </button>
          <button className="icon-btn" onClick={() => navigate('/shop/favorites')}>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
              <path d="M20.84 4.61a5.5 5.5 0 0 0-7.78 0L12 5.67l-1.06-1.06a5.5 5.5 0 0 0-7.78 7.78l1.06 1.06L12 21.23l7.78-7.78 1.06-1.06a5.5 5.5 0 0 0 0-7.78z"/>
            </svg>
          </button>
          <button className="icon-btn" onClick={() => navigate('/shop/cart')}>
            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
              <circle cx="9" cy="21" r="1"/>
              <circle cx="20" cy="21" r="1"/>
              <path d="M1 1h4l2.68 13.39a2 2 0 0 0 2 1.61h9.72a2 2 0 0 0 2-1.61L23 6H6"/>
            </svg>
          </button>
        </div>
      </div>

      {/* Main Layout: Sidebar + Content */}
      <div className="shop-layout-new">
        {/* Sidebar */}
        <aside className="shop-sidebar-new">
          {categories.map(cat => (
            <div
              key={cat.id}
              className={`category-item-new ${selectedCategory?.id === cat.id ? 'active' : ''}`}
              onClick={() => handleCategoryClick(cat)}
            >
              <div className="category-icon">
                {cat.name === 'Men' && ''}
                {cat.name === 'Women' && ''}
                {cat.name === 'Kids' && ''}
                {cat.name === 'Electronics' && ''}
                {cat.name === 'Home' && ''}
                {!['Men', 'Women', 'Kids', 'Electronics', 'Home'].includes(cat.name) && ''}
              </div>
              <div className="category-name">{cat.name}</div>
            </div>
          ))}
        </aside>

        {/* Main Content - Subcategories */}
        <main className="shop-content-new">
          {selectedCategory && (
            <>
              <div className="category-title-section">
                <h2>{selectedCategory.name.toUpperCase()}</h2>
              </div>

              {subcategories.length === 0 ? (
                <div className="no-subcategories">
                  <p>No subcategories available</p>
                </div>
              ) : (
                <>
                  {/* Top Wear Section Example */}
                  <div className="subcategory-section">
                    <h3 className="section-title">Categories</h3>
                    <div className="subcategory-grid">
                      {subcategories.map(sub => (
                        <div
                          key={sub.id}
                          className="subcategory-card"
                          onClick={() => handleSubcategoryClick(sub)}
                        >
                          <div className="subcategory-image">
                            <div className="placeholder-image">
                              {sub.name.substring(0, 2).toUpperCase()}
                            </div>
                          </div>
                          <div className="subcategory-name">{sub.name}</div>
                        </div>
                      ))}
                    </div>
                  </div>
                </>
              )}
            </>
          )}
        </main>
      </div>
    </div>
  );
};

export default ShopPage;
