import React, { useState } from 'react';
import axios from 'axios';
import { apiBaseUrl } from '../config';

const AdminApprovalModal = ({ 
  isOpen, 
  onClose, 
  itemType, // 'ad' or 'product'
  itemId, 
  itemName,
  onApprovalSuccess 
}) => {
  const [adminNote, setAdminNote] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState('');

  const handleSubmit = async (action) => {
    if (!adminNote.trim()) {
      setError('Admin note is required');
      return;
    }

    setIsLoading(true);
    setError('');

    try {
      const token = localStorage.getItem('adminToken');
      const endpoint = `${apiBaseUrl}/api/admin/${itemType}s/${itemId}/${action}`;
      
      const response = await axios.post(endpoint, {
        admin_note: adminNote.trim()
      }, {
        headers: { Authorization: `Bearer ${token}` }
      });

      if (response.status === 200) {
        onApprovalSuccess(response.data);
        onClose();
        setAdminNote('');
      }
    } catch (error) {
      console.error(`Error ${action}ing ${itemType}:`, error);
      setError(error.response?.data?.error || `Failed to ${action} ${itemType}`);
    } finally {
      setIsLoading(false);
    }
  };

  const handleClose = () => {
    setAdminNote('');
    setError('');
    onClose();
  };

  if (!isOpen) return null;

  return (
    <div className="modal-overlay" style={{
      position: 'fixed',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      background: 'rgba(0, 0, 0, 0.5)',
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'center',
      zIndex: 10000
    }}>
      <div style={{
        background: 'white',
        borderRadius: '12px',
        padding: '24px',
        maxWidth: '500px',
        width: '90%',
        maxHeight: '80vh',
        overflow: 'auto',
        boxShadow: '0 20px 40px rgba(0,0,0,0.3)'
      }}>
        {/* Header */}
        <div style={{
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          marginBottom: '20px',
          paddingBottom: '12px',
          borderBottom: '1px solid #e0e0e0'
        }}>
          <h2 style={{
            fontSize: '20px',
            fontWeight: 'bold',
            color: '#333',
            margin: 0
          }}>
            {itemType === 'ad' ? 'Advertisement' : 'Product'} Approval
          </h2>
          <button
            onClick={handleClose}
            style={{
              background: 'none',
              border: 'none',
              fontSize: '24px',
              cursor: 'pointer',
              color: '#666',
              padding: '0',
              width: '30px',
              height: '30px',
              display: 'flex',
              alignItems: 'center',
              justifyContent: 'center'
            }}
          >
            
          </button>
        </div>

        {/* Item Info */}
        <div style={{
          background: '#f8f9fa',
          padding: '12px',
          borderRadius: '8px',
          marginBottom: '20px'
        }}>
          <p style={{ margin: 0, fontSize: '14px', color: '#666' }}>
            <strong>Item:</strong> {itemName}
          </p>
          <p style={{ margin: '4px 0 0 0', fontSize: '14px', color: '#666' }}>
            <strong>ID:</strong> {itemId}
          </p>
        </div>

        {/* Admin Note Field */}
        <div style={{ marginBottom: '20px' }}>
          <label style={{
            display: 'block',
            marginBottom: '8px',
            fontWeight: 'bold',
            color: '#333',
            fontSize: '14px'
          }}>
            Admin Note <span style={{ color: '#e74c3c' }}>*</span>
          </label>
          <textarea
            value={adminNote}
            onChange={(e) => setAdminNote(e.target.value)}
            placeholder={`Enter your note for this ${itemType}...`}
            style={{
              width: '100%',
              minHeight: '120px',
              padding: '12px',
              border: '2px solid #e0e0e0',
              borderRadius: '8px',
              fontSize: '14px',
              fontFamily: 'inherit',
              resize: 'vertical',
              boxSizing: 'border-box'
            }}
            disabled={isLoading}
          />
          <p style={{
            fontSize: '12px',
            color: '#666',
            margin: '4px 0 0 0'
          }}>
            This note will be visible to the {itemType === 'ad' ? 'advertiser' : 'shop owner'}.
          </p>
        </div>

        {/* Error Message */}
        {error && (
          <div style={{
            background: '#fee',
            color: '#c33',
            padding: '12px',
            borderRadius: '8px',
            marginBottom: '20px',
            fontSize: '14px'
          }}>
            {error}
          </div>
        )}

        {/* Action Buttons */}
        <div style={{
          display: 'flex',
          gap: '12px',
          justifyContent: 'flex-end'
        }}>
          <button
            onClick={handleClose}
            disabled={isLoading}
            style={{
              padding: '12px 24px',
              background: '#6c757d',
              color: 'white',
              border: 'none',
              borderRadius: '8px',
              fontSize: '14px',
              fontWeight: 'bold',
              cursor: isLoading ? 'not-allowed' : 'pointer',
              opacity: isLoading ? 0.6 : 1
            }}
          >
            Cancel
          </button>
          
          <button
            onClick={() => handleSubmit('reject')}
            disabled={isLoading}
            style={{
              padding: '12px 24px',
              background: '#dc3545',
              color: 'white',
              border: 'none',
              borderRadius: '8px',
              fontSize: '14px',
              fontWeight: 'bold',
              cursor: isLoading ? 'not-allowed' : 'pointer',
              opacity: isLoading ? 0.6 : 1
            }}
          >
            {isLoading ? 'Processing...' : 'Reject'}
          </button>
          
          <button
            onClick={() => handleSubmit('approve')}
            disabled={isLoading}
            style={{
              padding: '12px 24px',
              background: '#28a745',
              color: 'white',
              border: 'none',
              borderRadius: '8px',
              fontSize: '14px',
              fontWeight: 'bold',
              cursor: isLoading ? 'not-allowed' : 'pointer',
              opacity: isLoading ? 0.6 : 1
            }}
          >
            {isLoading ? 'Processing...' : 'Approve'}
          </button>
        </div>
      </div>
    </div>
  );
};

export default AdminApprovalModal;
