import React, { useEffect, useMemo, useRef, useState, useCallback } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';

const ZEGO_SCRIPT_SRC = 'https://unpkg.com/@zegocloud/zego-uikit-prebuilt/zego-uikit-prebuilt.js';

const CallRoom = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const containerRef = useRef(null);
  const zegoInstanceRef = useRef(null);
  const scriptElRef = useRef(null);

  const [loading, setLoading] = useState(true);
  const [statusMessage, setStatusMessage] = useState('Preparing call...');
  const [loadingTitle, setLoadingTitle] = useState('Preparing your call');
  const [error, setError] = useState(null);

  const params = useMemo(() => {
    const query = new URLSearchParams(location.search);
    const get = (key) => {
      const value = query.get(key);
      return value ? decodeURIComponent(value) : '';
    };

    return {
      roomID: get('roomID'),
      userID: query.get('userID'),
      userName: get('userName') || 'User',
      zegoToken: get('zegoToken'),
      callType: (query.get('callType') || 'video').toLowerCase(),
      matchedUserId: query.get('matchedUserId') || '-',
      matchedUserName: get('matchedUserName') || 'Partner',
      sessionId: get('sessionId') || '-',
    };
  }, [location.search]);

  const handleLeave = useCallback(() => {
    navigate('/calls', { replace: true });
  }, [navigate]);

  useEffect(() => {
    const { roomID, userID, zegoToken } = params;

    if (!roomID || !userID || !zegoToken) {
      setError('Missing call information. Please start a new call.');
      setLoading(false);
      return;
    }

    const initialiseZego = () => {
      if (!window.ZegoUIKitPrebuilt) {
        setError('Call engine failed to load. Check your connection and try again.');
        setLoading(false);
        return;
      }

      try {
        setStatusMessage('Connecting to call engine');
        const instance = window.ZegoUIKitPrebuilt.create(zegoToken);
        zegoInstanceRef.current = instance;

        const isVideoCall = params.callType === 'video';
        instance.joinRoom({
          container: containerRef.current,
          scenario: { mode: window.ZegoUIKitPrebuilt.OneONoneCall },
          sharedLinks: [
            {
              name: 'Invite link',
              url: `${window.location.origin}${window.location.pathname}?roomID=${encodeURIComponent(params.roomID)}`,
            },
          ],
          maxUsers: 2,
          layout: 'Auto',
          showLayoutButton: false,
          showTextChat: false,
          showUserList: false,
          showRoomDetailsButton: false,
          showScreenSharingButton: false,
          turnOnMicrophoneWhenJoining: true,
          turnOnCameraWhenJoining: isVideoCall,
          showMyCameraToggleButton: true,
          showMyMicrophoneToggleButton: true,
          topMenuBarConfig: {
            buttons: [
              'toggleMicrophoneButton',
              'toggleCameraButton',
              'switchCameraButton',
              'flipCameraButton',
              'hangUpButton',
            ],
          },
          onJoinRoom: () => {
            setLoadingTitle('Connected!');
            setStatusMessage('Enjoy your call.');
            setTimeout(() => setLoading(false), 400);
          },
          onLeaveRoom: () => {
            handleLeave();
          },
          onUserJoin: (users) => {
            console.log('[CallRoom] Participant joined', users);
          },
          onUserLeave: (users) => {
            console.log('[CallRoom] Participant left', users);
          },
        });

        // Fallback hide overlay even if onJoinRoom doesn't fire
        setTimeout(() => setLoading(false), 5000);
      } catch (err) {
        console.error('[CallRoom] Failed to join room', err);
        setError('Unable to join the room. Please try again.');
        setLoading(false);
      }
    };

    if (window.ZegoUIKitPrebuilt) {
      initialiseZego();
    } else {
      setStatusMessage('Loading call engine');
      const script = document.createElement('script');
      script.src = ZEGO_SCRIPT_SRC;
      script.async = true;
      script.onload = initialiseZego;
      script.onerror = () => {
        setError('Unable to load the call engine. Please check your network and try again.');
        setLoading(false);
      };
      document.body.appendChild(script);
      scriptElRef.current = script;
    }

    return () => {
      if (zegoInstanceRef.current) {
        try {
          zegoInstanceRef.current.destroy();
        } catch (err) {
          console.warn('[CallRoom] Error cleaning up Zego instance', err);
        }
      }
      if (scriptElRef.current) {
        scriptElRef.current.remove();
      }
    };
  }, [params, handleLeave]);

  const initials = useMemo(() => {
    return params.matchedUserName
      .split(/\s+/)
      .filter(Boolean)
      .slice(0, 2)
      .map((word) => word[0]?.toUpperCase() || '')
      .join('') || 'UH';
  }, [params.matchedUserName]);

  return (
    <div className="call-room">
      {error && (
        <div className="call-room__error" role="alert">
          <div className="call-room__error-card">
            <h2>Unable to start the call</h2>
            <p>{error}</p>
            <button onClick={handleLeave}>Return to Calls</button>
          </div>
        </div>
      )}

      <header className="call-room__header">
        <div className="call-room__partner">
          <div className="call-room__avatar" aria-hidden>{initials}</div>
          <div>
            <h1>{params.matchedUserName}</h1>
            <span>Room {params.roomID}  Session {params.sessionId}</span>
          </div>
        </div>
        <div className="call-room__status">
          {params.callType === 'audio' ? 'Voice call  encrypted' : 'Video call  encrypted'}
        </div>
        <button type="button" className="call-room__leave" onClick={handleLeave}>
          Leave
        </button>
      </header>

      <main className="call-room__main">
        {loading && !error && (
          <div className="call-room__loading" role="status" aria-live="polite">
            <div className="call-room__spinner" />
            <h2>{loadingTitle}</h2>
            <p>{statusMessage}</p>
          </div>
        )}
        <div ref={containerRef} className="call-room__zego" />
      </main>
    </div>
  );
};

export default CallRoom;
