import React, { useState, useEffect } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import { apiBaseUrl } from '../config';

const OrderConfirmationPage = () => {
  const navigate = useNavigate();
  const { orderId } = useParams();
  const { token } = useAuth();
  const [order, setOrder] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchOrderDetails();
  }, [orderId]);

  const fetchOrderDetails = async () => {
    try {
      setLoading(true);
      const response = await fetch(`${apiBaseUrl}/api/shop/order/${orderId}`, {
        headers: {
          'Authorization': `Bearer ${token}`
        }
      });

      if (response.ok) {
        const data = await response.json();
        setOrder(data.order);
      }
    } catch (error) {
      console.error('Error fetching order:', error);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="order-confirmation-page page-with-bottom-nav">
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p>Loading...</p>
        </div>
      </div>
    );
  }

  if (!order) {
    return (
      <div className="order-confirmation-page page-with-bottom-nav">
        <div className="error-container">
          <p>Order not found</p>
          <button className="btn-primary" onClick={() => navigate('/shop')}>
            Back to Shop
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="order-confirmation-page page-with-bottom-nav">
      <div className="confirmation-content">
        <div className="confirmation-icon">
          <svg width="120" height="120" viewBox="0 0 120 120" fill="none" xmlns="http://www.w3.org/2000/svg">
            <circle cx="60" cy="60" r="55" fill="#10b981" opacity="0.1"/>
            <circle cx="60" cy="60" r="45" fill="#10b981" opacity="0.2"/>
            <path d="M45 60L55 70L75 50" stroke="#10b981" strokeWidth="6" strokeLinecap="round" strokeLinejoin="round"/>
          </svg>
        </div>
        
        <h1 className="confirmation-title">Order Confirmed!</h1>
        <p className="confirmation-message">Thank you for your purchase. Your order has been placed successfully.</p>

        <div className="order-details-card">
          <h2>Order Details</h2>
          <div className="order-detail-row">
            <span>Order Number:</span>
            <strong>{order.order_number}</strong>
          </div>
          <div className="order-detail-row">
            <span>Total Amount:</span>
            <strong>{parseFloat(order.total_amount).toFixed(2)}</strong>
          </div>
          <div className="order-detail-row">
            <span>Payment Status:</span>
            <span className={`status-badge ${order.payment_status === 'paid' ? 'paid' : 'pending'}`}>
              {order.payment_status === 'paid' ? 'Paid' : 'Pending'}
            </span>
          </div>
          <div className="order-detail-row">
            <span>Order Status:</span>
            <span className={`status-badge ${order.status}`}>
              {order.status.charAt(0).toUpperCase() + order.status.slice(1)}
            </span>
          </div>
        </div>

        {order.shipping_address && (
          <div className="order-details-card">
            <h2>Shipping Address</h2>
            <div className="shipping-address">
              <p><strong>{order.shipping_address.full_name}</strong></p>
              <p>{order.shipping_address.address_line1}</p>
              {order.shipping_address.address_line2 && <p>{order.shipping_address.address_line2}</p>}
              <p>{order.shipping_address.city}, {order.shipping_address.state} - {order.shipping_address.pincode}</p>
              <p>Phone: {order.shipping_address.phone}</p>
            </div>
          </div>
        )}

        {order.items && order.items.length > 0 && (
          <div className="order-details-card">
            <h2>Order Items</h2>
            {order.items.map((item, idx) => (
              <div key={idx} className="order-item">
                <div className="order-item-info">
                  <h3>{item.product_title}</h3>
                  <p>Quantity: {item.quantity}  {parseFloat(item.unit_price).toFixed(2)}</p>
                  <p className="order-item-total">{parseFloat(item.total_price).toFixed(2)}</p>
                </div>
              </div>
            ))}
          </div>
        )}

        <div className="confirmation-actions">
          <button className="btn-primary" onClick={() => navigate('/shop')}>
            Continue Shopping
          </button>
          <button className="btn-secondary" onClick={() => navigate('/shop/orders')}>
            View Orders
          </button>
        </div>
      </div>
    </div>
  );
};

export default OrderConfirmationPage;

