import React, { useState, useRef, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import axios from 'axios';
import { apiBaseUrl } from '../config';
import Step1RegistrationModal from './Step1RegistrationModal';

const OTPVerification = () => {
  const [otp, setOtp] = useState(['', '', '', '', '', '']);
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');
  const [resendTimer, setResendTimer] = useState(0);
  const [showStep1Modal, setShowStep1Modal] = useState(false);
  const [newUserData, setNewUserData] = useState(null);
  const navigate = useNavigate();
  const location = useLocation();
  const { login } = useAuth();
  const inputRefs = useRef([]);

  const phone = location.state?.phone;

  // Helper function to format phone number for display
  const formatPhoneForDisplay = (phoneNumber) => {
    if (!phoneNumber) return '';
    // If it already has +91, format it nicely
    if (phoneNumber.startsWith('+91')) {
      return phoneNumber;
    }
    // If it's just 10 digits, add +91
    if (phoneNumber.length === 10) {
      return `+91${phoneNumber}`;
    }
    return phoneNumber;
  };

  useEffect(() => {
    if (!phone) {
      navigate('/login');
      return;
    }

    // Start resend timer
    setResendTimer(30);
    const timer = setInterval(() => {
      setResendTimer((prev) => {
        if (prev <= 1) {
          clearInterval(timer);
          return 0;
        }
        return prev - 1;
      });
    }, 1000);

    return () => clearInterval(timer);
  }, [phone, navigate]);

  const handleOtpChange = (index, value) => {
    if (value.length > 1) return; // Only allow single digit

    const newOtp = [...otp];
    newOtp[index] = value;
    setOtp(newOtp);

    // Auto-focus next input
    if (value && index < 5) {
      inputRefs.current[index + 1]?.focus();
    }
  };

  const handleKeyDown = (index, e) => {
    if (e.key === 'Backspace' && !otp[index] && index > 0) {
      inputRefs.current[index - 1]?.focus();
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    const otpString = otp.join('');
    if (otpString.length !== 6) {
      setMessage({ type: 'error', text: 'Please enter complete OTP' });
      return;
    }

    setLoading(true);
    setMessage('');

    try {
      const response = await axios.post(`${apiBaseUrl}/api/verify-otp`, {
        phone,
        otp_code: otpString
      });

      if (response.data.user_exists) {
        // Existing user - login and redirect to calls
        console.log('Login successful, user data:', response.data.user);
        console.log('Access token:', response.data.access_token);
        
        // Test local storage before calling login
        try {
          localStorage.setItem('test', 'test-value');
          const testValue = localStorage.getItem('test');
          console.log('Local storage test:', testValue);
          localStorage.removeItem('test');
        } catch (storageError) {
          console.error('Local storage error:', storageError);
        }
        
        login(response.data.user, response.data.access_token);
        
        // Verify token was stored
        const storedToken = localStorage.getItem('authToken');
        const storedUser = localStorage.getItem('userData');
        console.log('Stored token:', storedToken);
        console.log('Stored user:', storedUser);
        
        navigate('/calls');
      } else {
        // New user - show step 1 registration modal
        setNewUserData({ phone });
        setShowStep1Modal(true);
      }
    } catch (error) {
      const errorMessage = error.response?.data?.error || 'Invalid OTP. Please try again.';
      setMessage({ type: 'error', text: errorMessage });
    } finally {
      setLoading(false);
    }
  };

  const handleResendOTP = async () => {
    if (resendTimer > 0) return;

    setLoading(true);
    setMessage('');

    try {
      // Phone already includes +91 from PhoneEntry component
      await axios.post(`${apiBaseUrl}/api/send-otp`, { phone });
      setMessage({ type: 'success', text: 'OTP resent successfully!' });
      setResendTimer(30);
    } catch (error) {
      const errorMessage = error.response?.data?.error || 'Failed to resend OTP. Please try again.';
      setMessage({ type: 'error', text: errorMessage });
    } finally {
      setLoading(false);
    }
  };

  const handleStep1Success = (userData) => {
    setShowStep1Modal(false);
    setNewUserData(null);
    navigate('/calls');
  };

  const handleStep1Close = () => {
    setShowStep1Modal(false);
    setNewUserData(null);
  };

  return (
    <div className="login-container">
      <div className="login-content">
        {/* 1. TOP: Logo and Brand Name */}
        <div className="header-section">
          <div className="brand-container">
            <img 
              src="https://akm-dv.github.io/unlimitedhub/logo.jpg" 
              alt="Dating Platform Logo" 
              className="brand-logo"
            />
            <h1 className="brand-name">Unlimited Hub</h1>
          </div>
        </div>

        {/* 2. BELOW: Illustration */}
        <div className="illustration-section">
          <img 
            src="https://cdni.iconscout.com/illustration/premium/thumb/online-dating-app-5155859-4309052.png" 
            alt="Dating App Illustration" 
            className="dating-illustration"
          />
        </div>

        {/* 3. BELOW: Content/Description */}
        <div>
          <h2 className="tagline-title">Verify Your Number</h2>
          <p className="tagline-subtitle">Enter the 6-digit code sent to {formatPhoneForDisplay(phone)}</p>
        </div>

        {/* 4. BOTTOM: OTP Form Container */}
        <div className="form-section">
          {message && (
            <div className={`message ${message.type}`}>
              {message.text}
            </div>
          )}

          <form onSubmit={handleSubmit} className="login-form">
            <div className="form-group">
              <label className="form-label">Enter 6-digit OTP</label>
              <div className="otp-container">
                {otp.map((digit, index) => (
                  <input
                    key={index}
                    ref={(el) => (inputRefs.current[index] = el)}
                    type="text"
                    className="otp-input"
                    value={digit}
                    onChange={(e) => handleOtpChange(index, e.target.value)}
                    onKeyDown={(e) => handleKeyDown(index, e)}
                    maxLength={1}
                    inputMode="numeric"
                    pattern="[0-9]*"
                  />
                ))}
              </div>
              <p style={{ fontSize: '0.8rem', color: '#666', marginTop: '0.5rem', textAlign: 'center' }}>
                Enter the verification code sent to your phone
              </p>
            </div>

            <button 
              type="submit" 
              className="primary-btn"
              disabled={loading || otp.join('').length !== 6}
            >
              {loading ? 'Verifying...' : 'Verify & Continue'}
            </button>
          </form>

          <div style={{ textAlign: 'center', marginTop: '1rem' }}>
            <p style={{ fontSize: '0.9rem', color: 'white', marginBottom: '0.5rem' }}>
              Didn't receive the code?
            </p>
            <button
              type="button"
              className="secondary-btn"
              onClick={handleResendOTP}
              disabled={resendTimer > 0 || loading}
            >
              {resendTimer > 0 
                ? `Resend in ${resendTimer}s` 
                : 'Resend OTP'
              }
            </button>
          </div>

          <div style={{ textAlign: 'center', marginTop: '1rem' }}>
            <button
              type="button"
              className="back-btn"
              onClick={() => navigate('/login')}
            >
               Back to Login
            </button>
          </div>
        </div>
      </div>

      {/* Step 1 Registration Modal */}
      <Step1RegistrationModal
        isOpen={showStep1Modal}
        onClose={handleStep1Close}
        phone={newUserData?.phone}
        onSuccess={handleStep1Success}
      />
    </div>
  );
};

export default OTPVerification; 