import React, { useState, useEffect } from 'react';
import ReactDOM from 'react-dom';
import { useAuth } from '../context/AuthContext';
import axios from 'axios';
import { apiBaseUrl } from '../config';

const Step2RegistrationModal = ({ isOpen, onClose, onSuccess }) => {
  const { updateUser, token } = useAuth();
  const [formData, setFormData] = useState({
    address: '',
    pincode: '',
    state: ''
  });
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!formData.address.trim()) {
      setMessage({ type: 'error', text: 'Address is required' });
      return;
    }
    
    if (!formData.pincode.trim()) {
      setMessage({ type: 'error', text: 'Pincode is required' });
      return;
    }
    
    if (!formData.state.trim()) {
      setMessage({ type: 'error', text: 'State is required' });
      return;
    }

    setLoading(true);
    setMessage('');

    try {
      const response = await axios.post(`${apiBaseUrl}/api/register/step2`, {
        address: formData.address.trim(),
        pincode: formData.pincode.trim(),
        state: formData.state.trim()
      }, {
        headers: { Authorization: `Bearer ${token}` }
      });

      // Update user data in context
      updateUser(response.data.user);
      
      setMessage({ type: 'success', text: 'Address details saved! You can now access the Shop panel.' });
      
      // Close modal after a short delay
      setTimeout(() => {
        onSuccess && onSuccess(response.data.user);
        onClose();
      }, 1500);

    } catch (error) {
      const errorMessage = error.response?.data?.error || 'Failed to save address details. Please try again.';
      setMessage({ type: 'error', text: errorMessage });
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
    }
    return () => {
      document.body.style.overflow = 'unset';
    };
  }, [isOpen]);

  if (!isOpen) return null;

  const modalContent = (
    <div 
      style={{
        position: 'fixed',
        top: '0',
        left: '0',
        right: '0',
        bottom: '0',
        backgroundColor: 'rgba(0, 0, 0, 0.75)',
        zIndex: '99999',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        padding: '20px',
        overflow: 'auto'
      }}
      onClick={(e) => e.target === e.currentTarget && onClose()}
    >
      <div 
        style={{
          backgroundColor: '#ffffff',
          borderRadius: '20px',
          width: '100%',
          maxWidth: '450px',
          maxHeight: '85vh',
          overflowY: 'auto',
          boxShadow: '0 25px 50px rgba(0, 0, 0, 0.25)',
          position: 'relative',
          margin: 'auto',
          transform: 'translateZ(0)'
        }}
        onClick={(e) => e.stopPropagation()}
      >
        <div className="modal-header">
          <h2>Add Your Address</h2>
          <p className="modal-subtitle">Step 2 of 3: Address Details</p>
        </div>

        <form onSubmit={handleSubmit} className="step-form">
          <div className="form-group">
            <label htmlFor="address" className="form-label">Address *</label>
            <textarea
              id="address"
              name="address"
              value={formData.address}
              onChange={handleInputChange}
              className="form-textarea"
              placeholder="Enter your complete address"
              rows="3"
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="pincode" className="form-label">Pincode *</label>
            <input
              type="text"
              id="pincode"
              name="pincode"
              value={formData.pincode}
              onChange={handleInputChange}
              className="form-input"
              placeholder="Enter your pincode"
              maxLength="6"
              pattern="[0-9]{6}"
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="state" className="form-label">State *</label>
            <input
              type="text"
              id="state"
              name="state"
              value={formData.state}
              onChange={handleInputChange}
              className="form-input"
              placeholder="Enter your state"
              required
            />
          </div>

          {message && (
            <div className={`message ${message.type}`}>
              {message.text}
            </div>
          )}

          <div className="modal-actions">
            <button
              type="button"
              className="secondary-btn"
              onClick={onClose}
              disabled={loading}
            >
              Cancel
            </button>
            <button
              type="submit"
              className="primary-btn"
              disabled={loading || !formData.address.trim() || !formData.pincode.trim() || !formData.state.trim()}
            >
              {loading ? 'Saving...' : 'Save & Continue'}
            </button>
          </div>
        </form>

        <div className="step-progress">
          <div className="progress-dots">
            <div className="dot completed"></div>
            <div className="dot active"></div>
            <div className="dot"></div>
          </div>
          <p className="progress-text">Access to Shop panel will be unlocked</p>
        </div>
      </div>
    </div>
  );

  return ReactDOM.createPortal(modalContent, document.body);
};

export default Step2RegistrationModal;
