import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { apiBaseUrl } from '../config';

const AdvertiserLogin = () => {
  const navigate = useNavigate();
  const [phone, setPhone] = useState('');
  const [otp, setOtp] = useState('');
  const [step, setStep] = useState('phone'); // 'phone' or 'otp'
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [advertiserExists, setAdvertiserExists] = useState(false);
  const [advertiserData, setAdvertiserData] = useState(null);

  const handleSendOTP = async (e) => {
    e.preventDefault();
    if (!phone || phone.length < 10) {
      setError('Please enter a valid phone number');
      return;
    }

    // Format phone number with +91 prefix if not already present
    const formattedPhone = phone.startsWith('+91') ? phone : `+91${phone}`;

    setLoading(true);
    setError('');

    try {
      const response = await fetch(`${apiBaseUrl}/api/advertiser/send-otp`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ phone: formattedPhone }),
      });

      const data = await response.json();

      if (response.ok) {
        // Log OTP to frontend console if available (development mode)
        if (data.otp) {
          console.log(' DEVELOPMENT MODE: Your Advertiser OTP is:', data.otp);
          console.log(' Phone:', formattedPhone);
          console.log(' Use this OTP to verify your advertiser account');
        }
        
        setStep('otp');
        setError('');
      } else {
        setError(data.error || 'Failed to send OTP');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const handleVerifyOTP = async (e) => {
    e.preventDefault();
    if (!otp || otp.length !== 6) {
      setError('Please enter a valid 6-digit OTP');
      return;
    }

    setLoading(true);
    setError('');

    // Format phone number with +91 prefix if not already present
    const formattedPhone = phone.startsWith('+91') ? phone : `+91${phone}`;

    try {
      const response = await fetch(`${apiBaseUrl}/api/advertiser/verify-otp`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ phone: formattedPhone, otp_code: otp }),
      });

      const data = await response.json();

      if (response.ok) {
        if (data.advertiser_exists) {
          // Existing advertiser - redirect to dashboard
          setAdvertiserData(data.advertiser);
          setAdvertiserExists(true);
          navigate('/advertiser/dashboard', { 
            state: { advertiser: data.advertiser } 
          });
        } else {
          // New advertiser - redirect to registration
          navigate('/advertiser/register', { 
            state: { phone } 
          });
        }
      } else {
        setError(data.error || 'Invalid OTP');
      }
    } catch (err) {
      setError('Network error. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const handleBackToPhone = () => {
    setStep('phone');
    setOtp('');
    setError('');
  };

  return (
    <div className="advertiser-login">
      <div className="advertiser-login-container">
        <div className="advertiser-login-header">
          <h1> Advertiser Login</h1>
          <p>Access your advertising dashboard and manage your campaigns</p>
        </div>

        {step === 'phone' ? (
          <form onSubmit={handleSendOTP} className="advertiser-login-form">
            <div className="form-group">
              <label htmlFor="phone">Phone Number</label>
              <div className="mobile-input-container">
                <span className="country-code">+91</span>
                <input
                  type="tel"
                  id="phone"
                  value={phone}
                  onChange={(e) => setPhone(e.target.value)}
                  placeholder="Enter your mobile number"
                  maxLength="10"
                  required
                />
              </div>
              <p className="helper-text">We'll send you a verification code</p>
            </div>

            {error && <div className="error-message">{error}</div>}

            <button
              type="submit"
              className="advertiser-login-btn"
              disabled={loading}
            >
              {loading ? 'Sending OTP...' : 'Send OTP'}
            </button>

            <div className="advertiser-login-footer">
              <p>
                Don't have an advertiser account?{' '}
                <span className="link" onClick={() => navigate('/advertiser/register')}>
                  Register here
                </span>
              </p>
              <p>
                <span className="link" onClick={() => navigate('/login')}>
                   Back to User Login
                </span>
              </p>
            </div>
          </form>
        ) : (
          <form onSubmit={handleVerifyOTP} className="advertiser-login-form">
            <div className="form-group">
              <label htmlFor="otp">Enter OTP</label>
              <input
                type="text"
                id="otp"
                value={otp}
                onChange={(e) => setOtp(e.target.value.replace(/\D/g, '').slice(0, 6))}
                placeholder="Enter 6-digit OTP"
                maxLength={6}
                required
              />
              <p className="otp-info">
                OTP sent to {phone}
              </p>
            </div>

            {error && <div className="error-message">{error}</div>}

            <button
              type="submit"
              className="advertiser-login-btn"
              disabled={loading}
            >
              {loading ? 'Verifying...' : 'Verify OTP'}
            </button>

            <div className="advertiser-login-footer">
              <p>
                <span className="link" onClick={handleBackToPhone}>
                   Change Phone Number
                </span>
              </p>
              <p>
                <span className="link" onClick={() => navigate('/advertiser/login')}>
                  Resend OTP
                </span>
              </p>
            </div>
          </form>
        )}
      </div>
    </div>
  );
};

export default AdvertiserLogin;
