import React, { useEffect, useState } from 'react';
import { Navigate, useNavigate } from 'react-router-dom';
import { apiBaseUrl } from '../config';

const ShopAdminProtectedRoute = ({ children }) => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();

  useEffect(() => {
    const verifyToken = async () => {
      const token = localStorage.getItem('shopAdminToken');
      
      if (!token) {
        setIsAuthenticated(false);
        setLoading(false);
        navigate('/shop-admin/login', { replace: true });
        return;
      }

      // Verify token by making a test API call
      try {
        const response = await fetch(`${apiBaseUrl}/api/shop-admin/profile`, {
          method: 'GET',
          headers: {
            'Authorization': `Bearer ${token}`
          }
        });

        if (response.ok || response.status === 200) {
          setIsAuthenticated(true);
        } else if (response.status === 401 || response.status === 403) {
          // Token is invalid or expired
          localStorage.removeItem('shopAdminToken');
          localStorage.removeItem('shopAdminProvider');
          setIsAuthenticated(false);
          navigate('/shop-admin/login', { replace: true });
        } else {
          // Other error - assume authenticated but API might have issues
          setIsAuthenticated(true);
        }
      } catch (error) {
        console.error('Error verifying token:', error);
        // On network error, still allow access (might be temporary)
        // But remove invalid tokens
        if (error.message.includes('401') || error.message.includes('403')) {
          localStorage.removeItem('shopAdminToken');
          localStorage.removeItem('shopAdminProvider');
          setIsAuthenticated(false);
          navigate('/shop-admin/login', { replace: true });
          return;
        }
        setIsAuthenticated(true); // Allow access on network errors
      } finally {
        setLoading(false);
      }
    };

    verifyToken();
  }, [navigate]);

  if (loading) {
    return (
      <div style={{
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center',
        height: '100vh',
        background: '#f5f5f5'
      }}>
        <div className="loading-spinner" style={{
          border: '4px solid #f3f3f3',
          borderTop: '4px solid #667eea',
          borderRadius: '50%',
          width: '40px',
          height: '40px',
          animation: 'spin 1s linear infinite'
        }}></div>
        <style>{`
          @keyframes spin {
            0% { transform: rotate(0deg); }
            100% { transform: rotate(360deg); }
          }
        `}</style>
      </div>
    );
  }

  if (!isAuthenticated) {
    return <Navigate to="/shop-admin/login" replace />;
  }

  return children;
};

export default ShopAdminProtectedRoute;

