import React, { useEffect, useState } from 'react';
import { apiBaseUrl } from '../config';

const ProviderVariantsManager = ({ product, onClose }) => {
  const [variants, setVariants] = useState([]);
  const [newType, setNewType] = useState('');
  const [newValues, setNewValues] = useState('');
  const token = localStorage.getItem('authToken');

  const fetchVariants = async () => {
    const res = await fetch(`${apiBaseUrl}/api/provider/products/${product.id}/variants`, {
      headers: { 'Authorization': `Bearer ${token}` }
    });
    const data = await res.json();
    setVariants(data.variants || []);
  };

  useEffect(() => { fetchVariants(); }, []);

  const addType = async () => {
    if (!newType.trim()) return alert('Enter variant type');
    const values = newValues.split(',').map(v => v.trim()).filter(Boolean);
    const res = await fetch(`${apiBaseUrl}/api/provider/products/${product.id}/variants`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${token}` },
      body: JSON.stringify({ variant_name: newType, values })
    });
    if (res.ok) {
      setNewType('');
      setNewValues('');
      fetchVariants();
    } else {
      const e = await res.json();
      alert(e.error || 'Failed');
    }
  };

  const deleteType = async (variantTypeId) => {
    if (!window.confirm('Delete this variant type?')) return;
    const res = await fetch(`${apiBaseUrl}/api/provider/products/variants/${variantTypeId}`, {
      method: 'DELETE',
      headers: { 'Authorization': `Bearer ${token}` }
    });
    if (res.ok) fetchVariants(); else alert('Failed');
  };

  const addValue = async (variantTypeId) => {
    const value = window.prompt('Enter value');
    if (!value) return;
    const res = await fetch(`${apiBaseUrl}/api/provider/products/variants/${variantTypeId}/values`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${token}` },
      body: JSON.stringify({ value })
    });
    if (res.ok) fetchVariants(); else alert('Failed');
  };

  const deleteValue = async (valueId) => {
    if (!window.confirm('Delete this value?')) return;
    const res = await fetch(`${apiBaseUrl}/api/provider/products/variants/values/${valueId}`, {
      method: 'DELETE',
      headers: { 'Authorization': `Bearer ${token}` }
    });
    if (res.ok) fetchVariants(); else alert('Failed');
  };

  const uploadImage = async (variantValueId, file) => {
    if (!file) return;
    const formData = new FormData();
    formData.append('file', file);
    const res = await fetch(`${apiBaseUrl}/api/provider/variant-values/${variantValueId}/images`, {
      method: 'POST',
      headers: { 'Authorization': `Bearer ${token}` },
      body: formData
    });
    if (res.ok) fetchVariants(); else alert('Upload failed');
  };

  const setPrimary = async (imageId, currentPrimary, displayOrder) => {
    const res = await fetch(`${apiBaseUrl}/api/provider/variant-images/${imageId}`, {
      method: 'PUT',
      headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${token}` },
      body: JSON.stringify({ is_primary: !currentPrimary, display_order: displayOrder })
    });
    if (res.ok) fetchVariants(); else alert('Failed');
  };

  const deleteImage = async (imageId) => {
    if (!window.confirm('Delete this image?')) return;
    const res = await fetch(`${apiBaseUrl}/api/provider/variant-images/${imageId}`, {
      method: 'DELETE',
      headers: { 'Authorization': `Bearer ${token}` }
    });
    if (res.ok) fetchVariants(); else alert('Failed');
  };

  return (
    <div className="modal-overlay" onClick={onClose}>
      <div className="modal-content" onClick={(e) => e.stopPropagation()} style={{ maxWidth: 700, maxHeight: '90vh', display: 'flex', flexDirection: 'column' }}>
        <div className="modal-header">
          <h3>Manage Variants - {product.title}</h3>
          <button className="close-btn" onClick={onClose}></button>
        </div>
        <div style={{ overflowY: 'auto', padding: 20 }}>
          <div style={{ background: '#f8f9fa', borderRadius: 8, padding: 15, marginBottom: 20 }}>
            <h4 style={{ marginTop: 0 }}>Add Variant Type</h4>
            <input placeholder="Variant type (e.g., Color)" value={newType} onChange={(e) => setNewType(e.target.value)} style={{ width: '100%', padding: 8, marginBottom: 8 }} />
            <input placeholder="Values (comma separated)" value={newValues} onChange={(e) => setNewValues(e.target.value)} style={{ width: '100%', padding: 8, marginBottom: 8 }} />
            <button className="btn-primary" onClick={addType}>Add Variant</button>
          </div>

          {variants.length === 0 ? (
            <p style={{ color: '#777' }}>No variants yet.</p>
          ) : variants.map(v => (
            <div key={v.id} style={{ border: '1px solid #eee', borderRadius: 8, padding: 12, marginBottom: 16 }}>
              <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                <strong>{v.name}</strong>
                <div>
                  <button className="btn-primary" onClick={() => addValue(v.id)} style={{ marginRight: 8 }}>+ Value</button>
                  <button className="btn-danger" onClick={() => deleteType(v.id)}>Delete Type</button>
                </div>
              </div>
              {(v.values || []).map(val => (
                <div key={val.id} style={{ background: '#f8f9fa', borderRadius: 6, padding: 10, marginTop: 10 }}>
                  <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                    <span style={{ fontWeight: 600 }}>{val.value}</span>
                    <button className="btn-danger" onClick={() => deleteValue(val.id)}>Delete Value</button>
                  </div>
                  <div style={{ marginTop: 8 }}>
                    <input type="file" accept="image/*" onChange={(e) => uploadImage(val.id, e.target.files[0])} />
                  </div>
                  <div style={{ display: 'flex', flexWrap: 'wrap', gap: 8, marginTop: 8 }}>
                    {(val.images || []).map(img => (
                      <div key={img.id} style={{ position: 'relative', border: img.is_primary ? '3px solid #28a745' : '1px solid #ddd', borderRadius: 4 }}>
                        <img src={`${apiBaseUrl}${img.image_url}`} alt="variant" style={{ width: 80, height: 80, objectFit: 'cover' }} />
                        <div style={{ position: 'absolute', bottom: 0, left: 0, right: 0, background: 'rgba(0,0,0,0.65)', color: 'white', fontSize: 10, display: 'flex', justifyContent: 'space-between', padding: 2 }}>
                          <button onClick={() => setPrimary(img.id, img.is_primary, img.display_order)} style={{ background: 'transparent', border: 'none', color: 'white', cursor: 'pointer' }}>{img.is_primary ? 'Unset' : 'Set Primary'}</button>
                          <button onClick={() => deleteImage(img.id)} style={{ background: 'transparent', border: 'none', color: 'white', cursor: 'pointer' }}></button>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              ))}
            </div>
          ))}
        </div>
        <div className="modal-actions" style={{ padding: 12, borderTop: '1px solid #eee', display: 'flex', justifyContent: 'flex-end' }}>
          <button className="btn-secondary" onClick={onClose}>Close</button>
        </div>
      </div>
    </div>
  );
};

export default ProviderVariantsManager;


