import React from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import './App.css';
import PhoneEntry from './components/PhoneEntry';
import OTPVerification from './components/OTPVerification';
import UserDetails from './components/UserDetails';
import ProfileEdit from './components/ProfileEdit';
import CallsPage from './components/CallsPage';
import CallRoom from './components/CallRoom';
import AdvertiserLogin from './components/AdvertiserLogin';
import AdvertiserRegister from './components/AdvertiserRegister';
import AdvertiserDashboard from './components/AdvertiserDashboard';
import AdminLogin from './components/AdminLogin';
import AdminPanel from './components/AdminPanel';
import AdminProtectedRoute from './components/AdminProtectedRoute';
import AdsGallery from './components/AdsGallery';
import AdDetailsPage from './components/AdDetailsPage';
import BottomNavigation from './components/BottomNavigation';
import ShopPage from './components/ShopPage';
import ProductListPage from './components/ProductListPage';
import ProductDetailPage from './components/ProductDetailPage';
import CheckoutPage from './components/CheckoutPage';
import OrderConfirmationPage from './components/OrderConfirmationPage';
import ProviderDashboard from './components/ProviderDashboard';
import ShopAdminRequestAccess from './components/ShopAdminRequestAccess';
import ShopAdminLogin from './components/ShopAdminLogin';
import ShopAdminDashboard from './components/ShopAdminDashboard';
import ShopAdminProtectedRoute from './components/ShopAdminProtectedRoute';
import { AuthProvider, useAuth } from './context/AuthContext';

function AuthRedirect() {
  const { isAuthenticated, loading } = useAuth();
  
  if (loading) {
    return <div style={{ 
      display: 'flex', 
      justifyContent: 'center', 
      alignItems: 'center', 
      height: '100vh',
      background: 'linear-gradient(135deg, #ff6b9d 0%, #c44569 100%)',
      color: 'white',
      fontSize: '1.2rem'
    }}>
      Loading...
    </div>;
  }
  
  return isAuthenticated ? <Navigate to="/calls" /> : <Navigate to="/login" />;
}

function PrivateRoute({ children }) {
  const { isAuthenticated, loading } = useAuth();
  
  if (loading) {
    return <div style={{ 
      display: 'flex', 
      justifyContent: 'center', 
      alignItems: 'center', 
      height: '100vh',
      background: 'linear-gradient(135deg, #ff6b9d 0%, #c44569 100%)',
      color: 'white',
      fontSize: '1.2rem'
    }}>
      Loading...
    </div>;
  }
  
  return isAuthenticated ? children : <Navigate to="/" />;
}

function App() {
  return (
    <AuthProvider>
      <Router>
        <div className="App">
          <Routes>
            {/* Admin Routes - PC Responsive */}
            <Route path="/admin/login" element={<AdminLogin />} />
            <Route path="/admin" element={
              <AdminProtectedRoute>
                <AdminPanel />
              </AdminProtectedRoute>
            } />
            <Route path="/admin/dashboard" element={
              <AdminProtectedRoute>
                <AdminPanel />
              </AdminProtectedRoute>
            } />
            
            {/* Shop Admin Routes - PC Responsive */}
            <Route path="/shop-admin/request-access" element={<ShopAdminRequestAccess />} />
            <Route path="/shop-admin/login" element={<ShopAdminLogin />} />
            <Route
              path="/shop-admin"
              element={
                <ShopAdminProtectedRoute>
                  <ShopAdminDashboard />
                </ShopAdminProtectedRoute>
              }
            />
            <Route
              path="/shop-admin/*"
              element={
                <ShopAdminProtectedRoute>
                  <ShopAdminDashboard />
                </ShopAdminProtectedRoute>
              }
            />
            
            {/* App Routes - Now accessible on both Mobile and Desktop */}
            <Route path="*" element={
                <Routes>
                  <Route path="/" element={<AuthRedirect />} />
                  <Route path="/login" element={<PhoneEntry />} />
                  <Route path="/otp" element={<OTPVerification />} />
                  <Route path="/details" element={<UserDetails />} />
                  <Route path="/profile-edit" element={<ProfileEdit />} />
                  
                  <Route 
                    path="/calls" 
                    element={
                      <PrivateRoute>
                        <CallsPage />
                        <BottomNavigation />
                      </PrivateRoute>
                    } 
                  />

                  <Route
                    path="/call"
                    element={
                      <PrivateRoute>
                        <CallRoom />
                      </PrivateRoute>
                    }
                  />
                  
            <Route
              path="/shop"
              element={
                <PrivateRoute>
                  <ShopPage />
                  <BottomNavigation />
                </PrivateRoute>
              }
            />
            <Route
              path="/shop/products"
              element={
                <PrivateRoute>
                  <ProductListPage />
                  <BottomNavigation />
                </PrivateRoute>
              }
            />
            <Route
              path="/shop/product/:productId"
              element={
                <PrivateRoute>
                  <ProductDetailPage />
                  <BottomNavigation />
                </PrivateRoute>
              }
            />
            <Route
              path="/shop/checkout/:productId"
              element={
                <PrivateRoute>
                  <CheckoutPage />
                  <BottomNavigation />
                </PrivateRoute>
              }
            />
            <Route
              path="/shop/order-confirmation/:orderId"
              element={
                <PrivateRoute>
                  <OrderConfirmationPage />
                  <BottomNavigation />
                </PrivateRoute>
              }
            />
            <Route
              path="/provider-dashboard"
              element={
                <PrivateRoute>
                  <ProviderDashboard />
                  <BottomNavigation />
                </PrivateRoute>
              }
            />
                  
                  {/* Advertiser Routes */}
                  <Route path="/advertiser/login" element={<AdvertiserLogin />} />
                  <Route path="/advertiser/register" element={<AdvertiserRegister />} />
                  <Route path="/advertiser/dashboard" element={<AdvertiserDashboard />} />
                  
                  {/* Ads Gallery Route */}
                  <Route 
                    path="/ads-gallery" 
                    element={
                      <PrivateRoute>
                        <AdsGallery />
                        <BottomNavigation />
                      </PrivateRoute>
                    } 
                  />
                  
                  {/* Ad Details Route */}
                  <Route 
                    path="/ad/:adId" 
                    element={
                      <PrivateRoute>
                        <AdDetailsPage />
                        <BottomNavigation />
                      </PrivateRoute>
                    } 
                  />
                </Routes>
            } />
          </Routes>
        </div>
      </Router>
    </AuthProvider>
  );
}

export default App; 