import React, { useState, useEffect } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { apiBaseUrl } from '../config';

const ProductListPage = () => {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const categoryId = searchParams.get('category');
  const subcategoryId = searchParams.get('subcategory');
  
  const [products, setProducts] = useState([]);
  const [categoryName, setCategoryName] = useState('');
  const [subcategoryName, setSubcategoryName] = useState('');
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchProducts();
  }, [categoryId, subcategoryId]);

  const fetchProducts = async () => {
    try {
      setLoading(true);
      const params = new URLSearchParams();
      if (categoryId) params.append('category_id', categoryId);
      if (subcategoryId) params.append('subcategory_id', subcategoryId);
      
      const response = await fetch(`${apiBaseUrl}/api/products?${params}`);
      if (response.ok) {
        const data = await response.json();
        setProducts(data.products || []);
        
        // Set category and subcategory names from first product
        if (data.products && data.products.length > 0) {
          setCategoryName(data.products[0].category_name || '');
          setSubcategoryName(data.products[0].subcategory_name || '');
        }
      }
    } catch (error) {
      console.error('Error fetching products:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleProductClick = (productId) => {
    navigate(`/shop/product/${productId}`);
  };

  if (loading) {
    return (
      <div className="product-list-page page-with-bottom-nav">
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p>Loading products...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="product-list-page page-with-bottom-nav">
      {/* Header with Back Button */}
      <div className="product-list-header">
        <button className="back-btn" onClick={() => navigate('/shop')}>
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
            <path d="M19 12H5M12 19l-7-7 7-7"/>
          </svg>
        </button>
        <div className="header-title">
          <h1>{subcategoryName || categoryName}</h1>
          {subcategoryName && <p className="breadcrumb">{categoryName} / {subcategoryName}</p>}
        </div>
        <button className="icon-btn" onClick={() => navigate('/shop/cart')}>
          <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
            <circle cx="9" cy="21" r="1"/>
            <circle cx="20" cy="21" r="1"/>
            <path d="M1 1h4l2.68 13.39a2 2 0 0 0 2 1.61h9.72a2 2 0 0 0 2-1.61L23 6H6"/>
          </svg>
        </button>
      </div>

      {/* Products Grid */}
      <div className="products-grid-mobile">
        {products.length === 0 ? (
          <div className="no-products-message">
            <div className="no-products-icon">
              <svg width="120" height="120" viewBox="0 0 120 120" fill="none" xmlns="http://www.w3.org/2000/svg">
                <circle cx="60" cy="60" r="55" fill="#f0f4ff" stroke="#667eea" strokeWidth="2" strokeDasharray="4 4"/>
                <rect x="35" y="35" width="50" height="50" rx="8" fill="white" stroke="#667eea" strokeWidth="2"/>
                <path d="M45 50H75M45 60H65M45 70H70" stroke="#667eea" strokeWidth="3" strokeLinecap="round"/>
                <circle cx="75" cy="45" r="8" fill="#ff6b6b"/>
                <path d="M72 42L78 48M78 42L72 48" stroke="white" strokeWidth="2" strokeLinecap="round"/>
              </svg>
            </div>
            <h2 className="no-products-title">No Products Found</h2>
            <p className="no-products-text">We couldn't find any products in this category right now. Explore other categories to discover amazing products!</p>
            <button className="browse-categories-btn" onClick={() => navigate('/shop')}>
              <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <circle cx="11" cy="11" r="8"/>
                <path d="m21 21-4.35-4.35"/>
              </svg>
              Browse Categories
            </button>
          </div>
        ) : (
          products.map(product => (
            <div
              key={product.id}
              className="product-card-mobile"
              onClick={() => handleProductClick(product.id)}
            >
              <div className="product-image-mobile">
                {product.images && product.images.length > 0 ? (
                  <img 
                    src={product.images[0].startsWith('http') ? product.images[0] : `${apiBaseUrl}${product.images[0]}`} 
                    alt={product.title}
                    onError={(e) => {
                      e.target.style.display = 'none';
                      e.target.nextSibling.style.display = 'flex';
                    }}
                  />
                ) : null}
                <div className="product-image-placeholder" style={{ display: product.images && product.images.length > 0 ? 'none' : 'flex' }}>
                  <span></span>
                </div>
              </div>
              <div className="product-info-mobile">
                <h3 className="product-title-mobile">{product.title}</h3>
                {product.description && (
                  <p className="product-description-mobile">
                    {product.description.length > 80 
                      ? `${product.description.substring(0, 80)}...` 
                      : product.description}
                  </p>
                )}
                {product.discounted_price ? (
                  <div className="product-price-mobile">
                    <span className="price-current">{product.discounted_price}</span>
                    <span className="price-original">{product.original_price}</span>
                  </div>
                ) : (
                  <div className="product-price-mobile">
                    <span className="price-current">{product.original_price}</span>
                  </div>
                )}
              </div>
            </div>
          ))
        )}
      </div>
    </div>
  );
};

export default ProductListPage;

