import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { apiBaseUrl } from '../config';

const PhoneEntry = () => {
  const [phone, setPhone] = useState('');
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState('');
  const [currentSlide, setCurrentSlide] = useState(0);
  const navigate = useNavigate();

  // Slider data - will be loaded from backend
  const [slides, setSlides] = useState([
    {
      id: 'dating',
      title: 'Find Your Perfect Match',
      subtitle: 'Connect with amazing people and start your journey to love',
      illustration: 'https://cdni.iconscout.com/illustration/premium/thumb/online-dating-app-5155859-4309052.png',
      alt: 'Dating App Illustration'
    },
    {
      id: 'shopping',
      title: 'Shop Everything You Need',
      subtitle: 'Discover amazing products and deals from trusted sellers',
      illustration: 'https://cdni.iconscout.com/illustration/premium/thumb/online-shopping-5155859-4309053.png',
      alt: 'Shopping App Illustration'
    },
    {
      id: 'ads',
      title: 'Promote Your Business',
      subtitle: 'Reach your target audience with effective advertising campaigns',
      illustration: 'https://cdni.iconscout.com/illustration/premium/thumb/digital-marketing-5155859-4309054.png',
      alt: 'Advertising Illustration'
    }
  ]);

  // Load slider images from backend
  const loadSliderImages = async () => {
    try {
      const response = await axios.get(`${apiBaseUrl}/api/admin/image-settings`);
      const imageSettings = response.data;
      
      // Update slides with images from backend
      const newSlides = [
        {
          id: 'dating',
          title: 'Find Your Perfect Match',
          subtitle: 'Connect with amazing people and start your journey to love',
          illustration: imageSettings.login_slider_image_1 || slides[0].illustration,
          alt: 'Dating App Illustration'
        },
        {
          id: 'shopping',
          title: 'Shop Everything You Need',
          subtitle: 'Discover amazing products and deals from trusted sellers',
          illustration: imageSettings.login_slider_image_2 || slides[1].illustration,
          alt: 'Shopping App Illustration'
        },
        {
          id: 'ads',
          title: 'Promote Your Business',
          subtitle: 'Reach your target audience with effective advertising campaigns',
          illustration: imageSettings.login_slider_image_3 || slides[2].illustration,
          alt: 'Advertising Illustration'
        }
      ];
      
      setSlides(newSlides);
    } catch (error) {
      if (error.response?.status === 401) {
        console.warn('Skipping image settings load on login due to missing admin auth');
        return;
      }
      console.error('Error loading slider images:', error);
      // Keep default images if loading fails
    }
  };

  // Load images on component mount
  useEffect(() => {
    loadSliderImages();
  }, []);

  // Auto-slide functionality
  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentSlide((prev) => {
        const nextSlide = prev + 1;
        return nextSlide >= slides.length ? 0 : nextSlide;
      });
    }, 4000); // Change slide every 4 seconds

    return () => clearInterval(interval);
  }, [slides.length]);

  // Handle manual slide change
  const goToSlide = (slideIndex) => {
    setCurrentSlide(slideIndex);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!phone.trim()) {
      setMessage({ type: 'error', text: 'Please enter your mobile number' });
      return;
    }

    // Ensure phone number has exactly 10 digits
    if (phone.length !== 10) {
      setMessage({ type: 'error', text: 'Please enter a valid 10-digit mobile number' });
      return;
    }

    setLoading(true);
    setMessage('');

    try {
      // Add +91 prefix for India
      const phoneWithCountryCode = `+91${phone}`;
      const response = await axios.post(`${apiBaseUrl}/api/send-otp`, { phone: phoneWithCountryCode });
      
      if (response.data.message) {
        setMessage({ type: 'success', text: 'OTP sent successfully!' });
        
        // Log OTP to frontend console if available (development mode)
        if (response.data.otp) {
          console.log(' DEVELOPMENT MODE: Your OTP is:', response.data.otp);
          console.log(' Phone:', phoneWithCountryCode);
          console.log(' Use this OTP to verify your phone number');
        }
        
        // Navigate to OTP verification page with phone number (including country code)
        navigate('/otp', { state: { phone: phoneWithCountryCode } });
      }
    } catch (error) {
      const errorMessage = error.response?.data?.error || 'Failed to send OTP. Please try again.';
      setMessage({ type: 'error', text: errorMessage });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="login-container">
      <div className="login-content">
        {/* 1. TOP: Logo and Brand Name */}
        <div className="header-section">
          <div className="brand-container">
            <img 
              src="https://akm-dv.github.io/unlimitedhub/logo.jpg" 
              alt="Unlimited Hub Logo" 
              className="brand-logo"
            />
            <h1 className="brand-name">Unlimited Hub</h1>
          </div>
        </div>

        {/* 2. SLIDER: Content Slider */}
        <div className="slider-container">
          <div className="slider-wrapper">
            <div 
              className="slider-content"
              style={{ transform: `translateX(-${currentSlide * 100}%)` }}
            >
              {slides.map((slide, index) => (
                <div key={slide.id} className="slide">
                  <div className="illustration-section">
                    <img 
                      src={slide.illustration} 
                      alt={slide.alt} 
                      className="dating-illustration"
                    />
                  </div>
                  <div className="slide-content">
                    <h2 className="tagline-title">{slide.title}</h2>
                    <p className="tagline-subtitle">{slide.subtitle}</p>
                  </div>
                </div>
              ))}
            </div>
          </div>
          
          {/* Slider Navigation Dots */}
          <div className="slider-dots">
            {slides.map((_, index) => (
              <button
                key={index}
                className={`slider-dot ${index === currentSlide ? 'active' : ''}`}
                onClick={() => goToSlide(index)}
                aria-label={`Go to slide ${index + 1}`}
              />
            ))}
          </div>
        </div>

        {/* 4. BOTTOM: Login Form Container */}
        <div className="form-section">
          {message && (
            <div className={`message ${message.type}`}>
              {message.text}
            </div>
          )}

          <form onSubmit={handleSubmit} className="login-form">
            <div className="form-group">
              <label className="form-label">Mobile Number</label>
              <div className="mobile-input-container">
                <span className="country-code">
                  +91
                </span>
                <input
                  type="tel"
                  className="form-input"
                  placeholder="Enter your mobile number"
                  value={phone}
                  onChange={(e) => setPhone(e.target.value)}
                  maxLength="10"
                />
              </div>
              <p className="helper-text">
                We'll send you a verification code
              </p>
            </div>

            <button 
              type="submit" 
              className="primary-btn"
              disabled={loading}
            >
              {loading ? 'Sending OTP...' : 'Continue'}
            </button>
          </form>

          <div className="terms-section">
            <p className="terms-text">
              By continuing, you agree to our{' '}
              <a href="#" className="terms-link">Terms of Service</a> and{' '}
              <a href="#" className="terms-link">Privacy Policy</a>
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default PhoneEntry; 