import React, { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import axios from 'axios';
import Step2RegistrationModal from './Step2RegistrationModal';
import Step3RegistrationModal from './Step3RegistrationModal';
import './BottomNavigation.css';

const BottomNavigation = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { user, updateUser } = useAuth();
  const [showStep2Modal, setShowStep2Modal] = useState(false);
  const [showStep3Modal, setShowStep3Modal] = useState(false);
  const [completionStatus, setCompletionStatus] = useState({
    step1_completed: false,
    step2_completed: false,
    step3_completed: false
  });

  useEffect(() => {
    if (user) {
      setCompletionStatus({
        step1_completed: user.step1_completed || false,
        step2_completed: user.step2_completed || false,
        step3_completed: user.step3_completed || false
      });
    }
  }, [user]);

  const navItems = [
    {
      id: 'calls',
      label: 'Calls',
      icon: (
        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
          <path d="M22 16.92v3a2 2 0 01-2.18 2 19.79 19.79 0 01-8.63-3.07 19.5 19.5 0 01-6-6 19.79 19.79 0 01-3.07-8.67A2 2 0 014.11 2h3a2 2 0 012 1.72 12.84 12.84 0 00.7 2.81 2 2 0 01-.45 2.11L8.09 9.91a16 16 0 006 6l1.27-1.27a2 2 0 012.11-.45 12.84 12.84 0 002.81.7A2 2 0 0122 16.92z"/>
        </svg>
      ),
      path: '/calls'
    },
    {
      id: 'ads',
      label: 'Ads',
      icon: (
        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
          <rect x="2" y="3" width="20" height="14" rx="2" ry="2"/>
          <line x1="8" y1="21" x2="16" y2="21"/>
          <line x1="12" y1="17" x2="12" y2="21"/>
        </svg>
      ),
      path: '/ads-gallery'
    },
    {
      id: 'shopping',
      label: 'Shop',
      icon: (
        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
          <path d="M16 11V7a4 4 0 00-8 0v4M5 9h14l1 12H4L5 9z"/>
        </svg>
      ),
      path: '/shop'
    }
  ];

  const handleNavClick = (item) => {
    // Check if user has completed required steps for each panel
    if (item.id === 'calls') {
      // Step 1 required for calls
      if (!completionStatus.step1_completed) {
        // This shouldn't happen as user should be redirected to step 1 first
        navigate('/login');
        return;
      }
      navigate(item.path);
    } else if (item.id === 'shopping') {
      // Step 2 required for shop
      if (!completionStatus.step1_completed) {
        navigate('/login');
        return;
      }
      if (!completionStatus.step2_completed) {
        setShowStep2Modal(true);
        return;
      }
      navigate(item.path);
    } else if (item.id === 'ads') {
      // Step 3 required for ads
      if (!completionStatus.step1_completed) {
        navigate('/login');
        return;
      }
      if (!completionStatus.step2_completed) {
        setShowStep2Modal(true);
        return;
      }
      if (!completionStatus.step3_completed) {
        setShowStep3Modal(true);
        return;
      }
      navigate(item.path);
    } else {
      navigate(item.path);
    }
  };

  const handleStep2Success = (userData) => {
    setShowStep2Modal(false);
    updateUser(userData);
    setCompletionStatus(prev => ({
      ...prev,
      step2_completed: true
    }));
  };

  const handleStep3Success = (userData) => {
    setShowStep3Modal(false);
    updateUser(userData);
    setCompletionStatus(prev => ({
      ...prev,
      step3_completed: true
    }));
  };

  const isActive = (path) => {
    return location.pathname === path;
  };

  return (
    <div className="bottom-navigation">
      {navItems.map((item) => (
        <button
          key={item.id}
          className={`nav-item ${isActive(item.path) ? 'active' : ''}`}
          onClick={() => handleNavClick(item)}
        >
          <div className="nav-icon">
            {item.icon}
          </div>
          <span className="nav-label">{item.label}</span>
        </button>
      ))}
      
      {/* Step 2 Registration Modal */}
      <Step2RegistrationModal
        isOpen={showStep2Modal}
        onClose={() => setShowStep2Modal(false)}
        onSuccess={handleStep2Success}
      />
      
      {/* Step 3 Registration Modal */}
      <Step3RegistrationModal
        isOpen={showStep3Modal}
        onClose={() => setShowStep3Modal(false)}
        onSuccess={handleStep3Success}
      />
    </div>
  );
};

export default BottomNavigation;
