import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { apiBaseUrl } from '../config';
import ProviderImageUpload from './ProviderImageUpload';

const ShopAdminDashboard = () => {
  const navigate = useNavigate();
  const [activeTab, setActiveTab] = useState('dashboard');
  const [stats, setStats] = useState({ total: 0, approved: 0, pending: 0, rejected: 0 });
  const [products, setProducts] = useState([]);
  const [profile, setProfile] = useState(null);
  const [loading, setLoading] = useState(true);
  const [showAddProduct, setShowAddProduct] = useState(false);
  const [categories, setCategories] = useState([]);
  const [subcategories, setSubcategories] = useState([]);
  const [notification, setNotification] = useState(null);
  const [productForm, setProductForm] = useState({
    title: '',
    category_id: '',
    subcategory_id: '',
    description: '',
    original_price: '',
    discounted_price: '',
    stock_quantity: '0',
    images: []
  });

  useEffect(() => {
    fetchDashboardData();
    fetchCategories();
  }, []);

  useEffect(() => {
    if (activeTab === 'products') {
      fetchProducts();
    } else if (activeTab === 'profile') {
      fetchProfile();
    } else if (activeTab === 'dashboard') {
      fetchDashboardData();
    }
  }, [activeTab]);

  useEffect(() => {
    if (productForm.category_id) {
      fetchSubcategories(productForm.category_id);
    } else {
      setSubcategories([]);
      setProductForm(prev => ({ ...prev, subcategory_id: '' }));
    }
  }, [productForm.category_id]);

  const fetchDashboardData = async () => {
    try {
      const token = localStorage.getItem('shopAdminToken');
      if (!token) {
        console.error('No token found');
        navigate('/shop-admin/login');
        return;
      }
      
      const response = await fetch(`${apiBaseUrl}/api/shop-admin/dashboard`, {
        method: 'GET',
        headers: { 
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        credentials: 'include'
      });
      
      console.log('Dashboard response status:', response.status);
      
      if (response.ok) {
        const data = await response.json();
        setStats(data.stats);
      } else if (response.status === 401 || response.status === 403) {
        // Token expired or invalid
        localStorage.removeItem('shopAdminToken');
        localStorage.removeItem('shopAdminProvider');
        navigate('/shop-admin/login');
      } else {
        const errorData = await response.json().catch(() => ({ error: 'Unknown error' }));
        console.error('Dashboard error:', errorData);
      }
    } catch (error) {
      console.error('Error fetching dashboard:', error);
      // Check if it's a network/CORS error
      if (error.message.includes('Failed to fetch') || error.message.includes('CORS')) {
        console.error('Network/CORS error - check backend CORS configuration');
      }
    } finally {
      setLoading(false);
    }
  };

  const fetchProducts = async () => {
    try {
      const token = localStorage.getItem('shopAdminToken');
      if (!token) {
        console.error('No token found for products');
        return;
      }
      
      const response = await fetch(`${apiBaseUrl}/api/shop-admin/products`, {
        method: 'GET',
        headers: { 
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        credentials: 'include'
      });
      
      if (response.ok) {
        const data = await response.json();
        setProducts(data.products || []);
      } else if (response.status === 401 || response.status === 403) {
        localStorage.removeItem('shopAdminToken');
        localStorage.removeItem('shopAdminProvider');
        navigate('/shop-admin/login');
      } else {
        console.error('Products error:', await response.json().catch(() => ({})));
      }
    } catch (error) {
      console.error('Error fetching products:', error);
    }
  };

  const fetchProfile = async () => {
    try {
      const token = localStorage.getItem('shopAdminToken');
      if (!token) {
        console.error('No token found for profile');
        return;
      }
      
      const response = await fetch(`${apiBaseUrl}/api/shop-admin/profile`, {
        method: 'GET',
        headers: { 
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json'
        },
        credentials: 'include'
      });
      
      if (response.ok) {
        const data = await response.json();
        setProfile(data.provider);
      } else if (response.status === 401 || response.status === 403) {
        localStorage.removeItem('shopAdminToken');
        localStorage.removeItem('shopAdminProvider');
        navigate('/shop-admin/login');
      } else {
        console.error('Profile error:', await response.json().catch(() => ({})));
      }
    } catch (error) {
      console.error('Error fetching profile:', error);
    }
  };

  const fetchCategories = async () => {
    try {
      const response = await fetch(`${apiBaseUrl}/api/categories`);
      if (response.ok) {
        const data = await response.json();
        setCategories(data.categories || []);
      }
    } catch (error) {
      console.error('Error fetching categories:', error);
    }
  };

  const fetchSubcategories = async (categoryId) => {
    try {
      const response = await fetch(`${apiBaseUrl}/api/subcategories?category_id=${categoryId}`);
      if (response.ok) {
        const data = await response.json();
        setSubcategories(data.subcategories || []);
      }
    } catch (error) {
      console.error('Error fetching subcategories:', error);
    }
  };

  const handleProductSubmit = async (e) => {
    e.preventDefault();
    
    if (!productForm.title || !productForm.category_id || !productForm.original_price) {
      setNotification({ type: 'error', message: 'Please fill in all required fields' });
      setTimeout(() => setNotification(null), 3000);
      return;
    }

    try {
      const token = localStorage.getItem('shopAdminToken');
      const submitData = {
        title: productForm.title,
        category_id: parseInt(productForm.category_id),
        subcategory_id: productForm.subcategory_id ? parseInt(productForm.subcategory_id) : null,
        description: productForm.description,
        original_price: parseFloat(productForm.original_price),
        discounted_price: productForm.discounted_price ? parseFloat(productForm.discounted_price) : null,
        stock_quantity: parseInt(productForm.stock_quantity) || 0,
        images: productForm.images.map(img => img.url)
      };

      const response = await fetch(`${apiBaseUrl}/api/shop-admin/products`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(submitData)
      });

      if (response.ok) {
        const data = await response.json();
        setShowAddProduct(false);
        setProductForm({
          title: '',
          category_id: '',
          subcategory_id: '',
          description: '',
          original_price: '',
          discounted_price: '',
          stock_quantity: '0',
          images: []
        });
        setNotification({ type: 'success', message: data.message || 'Product submitted successfully! Waiting for admin approval.' });
        setTimeout(() => setNotification(null), 3000);
        // Secret refresh
        if (activeTab === 'products') {
          fetchProducts();
        }
        fetchDashboardData();
      } else {
        const error = await response.json();
        setNotification({ type: 'error', message: error.error || 'Failed to submit product' });
        setTimeout(() => setNotification(null), 3000);
      }
    } catch (error) {
      console.error('Error submitting product:', error);
      setNotification({ type: 'error', message: 'Failed to submit product' });
      setTimeout(() => setNotification(null), 3000);
    }
  };

  const handleLogout = () => {
    localStorage.removeItem('shopAdminToken');
    localStorage.removeItem('shopAdminProvider');
    navigate('/shop-admin/login');
  };

  const getStatusBadge = (status) => {
    const statusConfig = {
      'approved': { color: '#10b981', bg: '#d1fae5', text: 'Approved' },
      'pending': { color: '#f59e0b', bg: '#fef3c7', text: 'Pending' },
      'rejected': { color: '#ef4444', bg: '#fee2e2', text: 'Rejected' }
    };
    
    const config = statusConfig[status] || statusConfig['pending'];
    return (
      <span className="status-badge" style={{ color: config.color, backgroundColor: config.bg }}>
        {config.text}
      </span>
    );
  };

  if (loading) {
    return (
      <div className="shop-admin-dashboard" style={{ 
        minHeight: '100vh', 
        background: '#f5f5f5',
        display: 'flex',
        justifyContent: 'center',
        alignItems: 'center'
      }}>
        <div style={{ textAlign: 'center' }}>
          <div className="loading-spinner" style={{
            border: '4px solid #f3f3f3',
            borderTop: '4px solid #667eea',
            borderRadius: '50%',
            width: '50px',
            height: '50px',
            animation: 'spin 1s linear infinite',
            margin: '0 auto 20px'
          }}></div>
          <p style={{ color: '#666', fontSize: '16px' }}>Loading...</p>
          <style>{`
            @keyframes spin {
              0% { transform: rotate(0deg); }
              100% { transform: rotate(360deg); }
            }
          `}</style>
        </div>
      </div>
    );
  }

  return (
    <div className="shop-admin-dashboard">
      {/* Notification */}
      {notification && (
        <div className={`notification ${notification.type}`} style={{
          position: 'fixed',
          top: '20px',
          right: '20px',
          padding: '15px 20px',
          borderRadius: '8px',
          backgroundColor: notification.type === 'success' ? '#10b981' : '#ef4444',
          color: 'white',
          boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
          zIndex: 9999,
          animation: 'slideIn 0.3s ease-out'
        }}>
          {notification.message}
        </div>
      )}
      
      {/* Header */}
      <div className="shop-admin-dashboard-header">
        <h1>Shop Admin Panel</h1>
        <button className="btn-logout" onClick={handleLogout}>Logout</button>
      </div>

      <div className="shop-admin-layout">
        {/* Sidebar */}
        <aside className="shop-admin-sidebar">
          <nav className="shop-admin-nav">
            <button 
              className={`nav-item ${activeTab === 'dashboard' ? 'active' : ''}`}
              onClick={() => setActiveTab('dashboard')}
            >
              <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <rect x="3" y="3" width="7" height="7"/>
                <rect x="14" y="3" width="7" height="7"/>
                <rect x="14" y="14" width="7" height="7"/>
                <rect x="3" y="14" width="7" height="7"/>
              </svg>
              Dashboard
            </button>
            <button 
              className={`nav-item ${activeTab === 'add-product' ? 'active' : ''}`}
              onClick={() => {
                setActiveTab('add-product');
                setShowAddProduct(true);
              }}
            >
              <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <line x1="12" y1="5" x2="12" y2="19"/>
                <line x1="5" y1="12" x2="19" y2="12"/>
              </svg>
              Add Product
            </button>
            <button 
              className={`nav-item ${activeTab === 'products' ? 'active' : ''}`}
              onClick={() => setActiveTab('products')}
            >
              <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <path d="M6 2L3 6v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2V6l-3-4z"/>
                <line x1="3" y1="6" x2="21" y2="6"/>
                <path d="M16 10a4 4 0 0 1-8 0"/>
              </svg>
              My Products
            </button>
            <button 
              className={`nav-item ${activeTab === 'profile' ? 'active' : ''}`}
              onClick={() => setActiveTab('profile')}
            >
              <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                <path d="M20 21v-2a4 4 0 0 0-4-4H8a4 4 0 0 0-4 4v2"/>
                <circle cx="12" cy="7" r="4"/>
              </svg>
              Profile
            </button>
          </nav>
        </aside>

        {/* Main Content */}
        <main className="shop-admin-content">
          {activeTab === 'dashboard' && (
            <div className="dashboard-view">
              <h2>Dashboard Overview</h2>
              <div className="stats-grid">
                <div className="stat-card">
                  <div className="stat-icon" style={{ backgroundColor: '#667eea20', color: '#667eea' }}>
                    <svg width="32" height="32" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                      <path d="M6 2L3 6v14a2 2 0 0 0 2 2h14a2 2 0 0 0 2-2V6l-3-4z"/>
                      <line x1="3" y1="6" x2="21" y2="6"/>
                      <path d="M16 10a4 4 0 0 1-8 0"/>
                    </svg>
                  </div>
                  <div className="stat-info">
                    <h3>{stats.total}</h3>
                    <p>Total Products</p>
                  </div>
                </div>

                <div className="stat-card">
                  <div className="stat-icon" style={{ backgroundColor: '#10b98120', color: '#10b981' }}>
                    <svg width="32" height="32" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                      <polyline points="20 6 9 17 4 12"/>
                    </svg>
                  </div>
                  <div className="stat-info">
                    <h3>{stats.approved}</h3>
                    <p>Approved</p>
                  </div>
                </div>

                <div className="stat-card">
                  <div className="stat-icon" style={{ backgroundColor: '#f59e0b20', color: '#f59e0b' }}>
                    <svg width="32" height="32" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                      <circle cx="12" cy="12" r="10"/>
                      <line x1="12" y1="16" x2="12" y2="12"/>
                      <line x1="12" y1="8" x2="12.01" y2="8"/>
                    </svg>
                  </div>
                  <div className="stat-info">
                    <h3>{stats.pending}</h3>
                    <p>Pending</p>
                  </div>
                </div>

                <div className="stat-card">
                  <div className="stat-icon" style={{ backgroundColor: '#ef444420', color: '#ef4444' }}>
                    <svg width="32" height="32" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2">
                      <circle cx="12" cy="12" r="10"/>
                      <line x1="15" y1="9" x2="9" y2="15"/>
                      <line x1="9" y1="9" x2="15" y2="15"/>
                    </svg>
                  </div>
                  <div className="stat-info">
                    <h3>{stats.rejected}</h3>
                    <p>Rejected</p>
                  </div>
                </div>
              </div>
            </div>
          )}

          {activeTab === 'add-product' && !showAddProduct && (
            <div className="add-product-view">
              <div className="view-header">
                <h2>Add New Product</h2>
                <button className="btn-primary" onClick={() => setShowAddProduct(true)}>+ Add Product</button>
              </div>
              <div className="no-products">
                <p>Click "Add Product" to start adding products</p>
              </div>
            </div>
          )}

          {activeTab === 'add-product' && showAddProduct && (
            <div className="add-product-view">
              <div className="view-header">
                <h2>Add New Product</h2>
                <button className="btn-secondary" onClick={() => {
                  setShowAddProduct(false);
                  setProductForm({
                    title: '',
                    category_id: '',
                    subcategory_id: '',
                    description: '',
                    original_price: '',
                    discounted_price: '',
                    stock_quantity: '0',
                    images: []
                  });
                }}>Cancel</button>
              </div>

              <form onSubmit={handleProductSubmit} className="product-form">
                <div className="form-row">
                  <div className="form-group">
                    <label>Product Name *</label>
                    <input
                      type="text"
                      value={productForm.title}
                      onChange={(e) => setProductForm({...productForm, title: e.target.value})}
                      placeholder="Enter product name"
                      required
                    />
                  </div>
                </div>

                <div className="form-row">
                  <div className="form-group">
                    <label>Category *</label>
                    <select
                      value={productForm.category_id}
                      onChange={(e) => setProductForm({...productForm, category_id: e.target.value, subcategory_id: ''})}
                      required
                    >
                      <option value="">Select Category</option>
                      {categories.map(cat => (
                        <option key={cat.id} value={cat.id}>{cat.name}</option>
                      ))}
                    </select>
                  </div>

                  <div className="form-group">
                    <label>Subcategory (Optional)</label>
                    <select
                      value={productForm.subcategory_id}
                      onChange={(e) => setProductForm({...productForm, subcategory_id: e.target.value})}
                      disabled={!productForm.category_id}
                    >
                      <option value="">Select Subcategory</option>
                      {subcategories.map(sub => (
                        <option key={sub.id} value={sub.id}>{sub.name}</option>
                      ))}
                    </select>
                  </div>
                </div>

                <div className="form-group">
                  <label>Description</label>
                  <textarea
                    value={productForm.description}
                    onChange={(e) => setProductForm({...productForm, description: e.target.value})}
                    placeholder="Enter product description"
                    rows="4"
                  />
                </div>

                <div className="form-row">
                  <div className="form-group">
                    <label>Original Price () *</label>
                    <input
                      type="number"
                      step="0.01"
                      min="0"
                      value={productForm.original_price}
                      onChange={(e) => setProductForm({...productForm, original_price: e.target.value})}
                      placeholder="0.00"
                      required
                    />
                  </div>

                  <div className="form-group">
                    <label>Discounted Price ()</label>
                    <input
                      type="number"
                      step="0.01"
                      min="0"
                      value={productForm.discounted_price}
                      onChange={(e) => setProductForm({...productForm, discounted_price: e.target.value})}
                      placeholder="0.00 (optional)"
                    />
                  </div>

                  <div className="form-group">
                    <label>Stock Quantity</label>
                    <input
                      type="number"
                      min="0"
                      value={productForm.stock_quantity}
                      onChange={(e) => setProductForm({...productForm, stock_quantity: e.target.value})}
                      placeholder="0"
                    />
                  </div>
                </div>

                <div className="form-group">
                  <label>Product Images</label>
                  <ProviderImageUpload
                    images={productForm.images}
                    onImagesChange={(images) => setProductForm({...productForm, images})}
                    maxImages={5}
                  />
                </div>

                <div className="form-actions">
                  <button type="submit" className="btn-primary">Submit for Approval</button>
                  <button 
                    type="button" 
                    className="btn-secondary" 
                    onClick={() => {
                      setShowAddProduct(false);
                      setProductForm({
                        title: '',
                        category_id: '',
                        subcategory_id: '',
                        description: '',
                        original_price: '',
                        discounted_price: '',
                        stock_quantity: '0',
                        images: []
                      });
                    }}
                  >
                    Cancel
                  </button>
                </div>
              </form>
            </div>
          )}

          {activeTab === 'products' && (
            <div className="products-view">
              <div className="view-header">
                <h2>My Products</h2>
                <button className="btn-primary" onClick={() => {
                  setActiveTab('add-product');
                  setShowAddProduct(true);
                }}>+ Add Product</button>
              </div>

              {products.length === 0 ? (
                <div className="no-products">
                  <p>No products uploaded yet</p>
                  <button className="btn-primary" onClick={() => {
                    setActiveTab('add-product');
                    setShowAddProduct(true);
                  }}>Add Your First Product</button>
                </div>
              ) : (
                <div className="products-table">
                  <table>
                    <thead>
                      <tr>
                        <th>Product Name</th>
                        <th>Category</th>
                        <th>Status</th>
                        <th>Admin Message</th>
                        <th>Date Added</th>
                      </tr>
                    </thead>
                    <tbody>
                      {products.map(product => (
                        <tr key={product.id}>
                          <td>
                            <div className="product-name-cell">
                              {product.images && product.images.length > 0 && (
                                <img 
                                  src={product.images[0].startsWith('http') ? product.images[0] : `${apiBaseUrl}${product.images[0]}`}
                                  alt={product.title}
                                  className="product-thumb"
                                />
                              )}
                              <span>{product.title}</span>
                            </div>
                          </td>
                          <td>
                            {product.category_name}
                            {product.subcategory_name && ` / ${product.subcategory_name}`}
                          </td>
                          <td>{getStatusBadge(product.approval_status)}</td>
                          <td>{product.admin_note || '-'}</td>
                          <td>{new Date(product.created_at).toLocaleDateString()}</td>
                        </tr>
                      ))}
                    </tbody>
                  </table>
                </div>
              )}
            </div>
          )}

          {activeTab === 'profile' && profile && (
            <div className="profile-view">
              <h2>Profile Information</h2>
              <div className="profile-card">
                <div className="profile-row">
                  <span className="label">Shop Name:</span>
                  <span className="value">{profile.business_name}</span>
                </div>
                <div className="profile-row">
                  <span className="label">Owner Name:</span>
                  <span className="value">{profile.owner_name || '-'}</span>
                </div>
                <div className="profile-row">
                  <span className="label">Email:</span>
                  <span className="value">{profile.business_email}</span>
                </div>
                <div className="profile-row">
                  <span className="label">Phone:</span>
                  <span className="value">{profile.business_phone}</span>
                </div>
                <div className="profile-row">
                  <span className="label">Address:</span>
                  <span className="value">{profile.business_address}</span>
                </div>
                <div className="profile-row">
                  <span className="label">Status:</span>
                  <span className="value">
                    {profile.is_approved ? (
                      <span className="status-badge" style={{ color: '#10b981', backgroundColor: '#d1fae5' }}>
                        Approved
                      </span>
                    ) : (
                      <span className="status-badge" style={{ color: '#f59e0b', backgroundColor: '#fef3c7' }}>
                        Pending
                      </span>
                    )}
                  </span>
                </div>
                {profile.rejection_message && (
                  <div className="profile-row">
                    <span className="label">Rejection Reason:</span>
                    <span className="value error">{profile.rejection_message}</span>
                  </div>
                )}
                <div className="profile-row">
                  <span className="label">Member Since:</span>
                  <span className="value">{new Date(profile.created_at).toLocaleDateString()}</span>
                </div>
              </div>
            </div>
          )}
        </main>
      </div>
    </div>
  );
};

export default ShopAdminDashboard;

