import React, { useState } from 'react';
import { apiBaseUrl } from '../config';

const ProviderImageUpload = ({ images = [], onImagesChange, maxImages = 5, disabled = false }) => {
  const [uploading, setUploading] = useState(false);
  const [dragOver, setDragOver] = useState(false);

  const handleFileUpload = async (files) => {
    if (disabled) return;
    const fileArray = Array.from(files);
    if (images.length + fileArray.length > maxImages) {
      alert(`Maximum ${maxImages} images allowed`);
      return;
    }
    setUploading(true);
    try {
      // Get token - try shopAdminToken first, then authToken (for compatibility)
      let token = localStorage.getItem('shopAdminToken');
      if (!token) {
        token = localStorage.getItem('authToken');
      }
      
      if (!token || token === 'null') {
        throw new Error('Authentication token not found. Please log in again.');
      }
      
      const uploadPromises = fileArray.map(async (file) => {
        const formData = new FormData();
        formData.append('file', file);
        
        const response = await fetch(`${apiBaseUrl}/api/provider/upload-image`, {
          method: 'POST',
          headers: {
            'Authorization': `Bearer ${token}`
          },
          body: formData
        });
        
        // Handle token expiration
        if (response.status === 401 || response.status === 422) {
          const errorData = await response.json().catch(() => ({ error: 'Token expired or invalid' }));
          if (errorData.error && (errorData.error.includes('expired') || errorData.error.includes('invalid') || errorData.error.includes('token'))) {
            // Clear tokens and redirect to login
            localStorage.removeItem('shopAdminToken');
            localStorage.removeItem('authToken');
            localStorage.removeItem('shopAdminProvider');
            throw new Error('Session expired. Please log in again.');
          }
        }
        
        if (!response.ok) {
          const error = await response.json();
          throw new Error(error.error || 'Upload failed');
        }
        const result = await response.json();
        return { filename: result.filename, url: result.url, size: result.size };
      });
      const uploadedImages = await Promise.all(uploadPromises);
      onImagesChange([...(images || []), ...uploadedImages]);
    } catch (e) {
      alert(e.message);
      // If token expired, redirect to login
      if (e.message.includes('Session expired') || e.message.includes('log in')) {
        window.location.href = '/shop-admin/login';
      }
    } finally {
      setUploading(false);
    }
  };

  const handleDeleteImage = async (imageToDelete) => {
    if (disabled) return;
    if (!window.confirm('Delete this image?')) return;
    try {
      // Get token - try shopAdminToken first, then authToken (for compatibility)
      let token = localStorage.getItem('shopAdminToken');
      if (!token) {
        token = localStorage.getItem('authToken');
      }
      
      if (!token || token === 'null') {
        throw new Error('Authentication token not found. Please log in again.');
      }
      
      const response = await fetch(`${apiBaseUrl}/api/provider/delete-image`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${token}` },
        body: JSON.stringify({ filename: imageToDelete.filename })
      });
      
      // Handle token expiration
      if (response.status === 401 || response.status === 422) {
        const errorData = await response.json().catch(() => ({ error: 'Token expired or invalid' }));
        if (errorData.error && (errorData.error.includes('expired') || errorData.error.includes('invalid') || errorData.error.includes('token'))) {
          localStorage.removeItem('shopAdminToken');
          localStorage.removeItem('authToken');
          localStorage.removeItem('shopAdminProvider');
          throw new Error('Session expired. Please log in again.');
        }
      }
      
      if (!response.ok) {
        const error = await response.json();
        throw new Error(error.error || 'Delete failed');
      }
      onImagesChange((images || []).filter(img => img.filename !== imageToDelete.filename));
    } catch (e) {
      alert(e.message);
      // If token expired, redirect to login
      if (e.message.includes('Session expired') || e.message.includes('log in')) {
        window.location.href = '/shop-admin/login';
      }
    }
  };

  const handleDrop = (e) => {
    e.preventDefault();
    setDragOver(false);
    handleFileUpload(e.dataTransfer.files);
  };

  return (
    <div className="image-upload-container">
      <label className="form-label">Product Images</label>
      <div
        className={`upload-area ${dragOver ? 'drag-over' : ''} ${disabled ? 'disabled' : ''}`}
        onDragOver={(e) => { e.preventDefault(); setDragOver(true); }}
        onDragLeave={(e) => { e.preventDefault(); setDragOver(false); }}
        onDrop={handleDrop}
      >
        {uploading ? (
          <div><div className="loading-spinner"></div><p>Uploading images...</p></div>
        ) : (
          <div>
            <div style={{ fontSize: '24px', marginBottom: '10px' }}></div>
            <p><strong>Click to upload</strong> or drag and drop images</p>
            <input type="file" multiple accept="image/*" id="prov-image-input" style={{ display: 'none' }} onChange={(e) => handleFileUpload(e.target.files)} />
            <label htmlFor="prov-image-input" style={{ display: 'inline-block', marginTop: '10px', padding: '8px 16px', background: '#007bff', color: 'white', borderRadius: '4px', cursor: 'pointer' }}>Choose Images</label>
          </div>
        )}
      </div>
      {images && images.length > 0 && (
        <div className="image-preview-grid">
          {images.map((img, idx) => (
            <div key={idx} className="image-preview-item">
              <img src={`${apiBaseUrl}${img.url}`} alt={`Product ${idx+1}`} />
              {!disabled && (
                <button type="button" onClick={() => handleDeleteImage(img)} style={{ position: 'absolute', top: 5, right: 5, background: 'rgba(255,0,0,0.8)', color: 'white', border: 'none', borderRadius: '50%', width: 24, height: 24, cursor: 'pointer' }}></button>
              )}
              <div style={{ position: 'absolute', bottom: 0, left: 0, right: 0, background: 'rgba(0,0,0,0.7)', color: 'white', fontSize: 10, padding: 4, textAlign: 'center' }}>{img.filename}</div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default ProviderImageUpload;


